/* ui_common.scss */

/* ====== 버튼 ======  적용 */
.btn {  position: relative; box-sizing: border-box; 
    display: inline-block; padding:8px 10px 8px;
    margin-bottom: 0; min-width: 80px;
    font-size: 16px; font-weight: 400;line-height: 1.42857143;text-align: center;
    letter-spacing: -1px; white-space: nowrap; vertical-align: middle;    
    -ms-touch-action: manipulation;touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;-moz-user-select: none;-ms-user-select: none;
    user-select: none; background-image: none;
    border: 1px solid transparent;
    @include border-radius(3px); 
    -webkit-appearance: none;
    -moz-appearance: none;
}

// 비활성화
.btn:disabled{ @include opacity(0.5); pointer-events:none; }


// 색깔
.btn.btn_default { font-size: 16px; 
    background:#fff; border:1px solid #bfbfbf; color:$colorB;
}
.btn.btn_default:hover {  background:#fff;  border-color: #afafaf;}

.btn.btn_gray { font-size: 16px; 
    background:#757575; border:1px solid #757575; color:#fff;}
.btn.btn_gray:hover {  background:#656565; border-color:#656565;}

.btn.btn_black { font-size: 16px; 
    background:#444444; border:1px solid #444444; color:#fff;}
.btn.btn_black:hover {  background:#111111;  border-color: #111111;}

.btn.btn_dark { font-size: 16px; 
    background:#3b444e; border:1px solid #3b444e; color:#fff;}
.btn.btn_dark:hover {  background:#1b1f24;  border-color: #1b1f24;}


/* 적용 */
.btn.btn_blue { font-size: 16px; 
    background:#1c4bb7; border:1px solid #1c4bb7; color:#fff;}
.btn.btn_blue:hover { background:#034ba7;  border-color: #034ba7;}


// 크기
.btn.btn_lg { padding:15px 20px 15px;  min-width: 200px !important; font-size: 20px; }
.btn.btn_md { padding:13px 10px 13px;  font-size: 16px; }
.btn.btn_sm { padding: 6px 10px 7px;   min-width: 60px;  font-size: 14px; }
.btn.btn_xs { padding: 6px 10px 7px;   min-width: 60px;  font-size: 14px; }
.btn.btn_xs { padding: 4px 10px 4px;   min-width: 60px;  font-size: 14px; }

// 라운딩 
.btn.br { border-radius: 0px; }

//텍스트옆 추가아이콘
.icon_plus {
    padding: 9px 0px 9px !important;
    i { background:url('/resources/images/common/icon_plus01.png') no-repeat right center; padding-right:25px;}
}

// icon_btn
.btn.btn_md.icon_btn {
    position: relative; border: 0;
    width:50px; min-width: 50px; height: 50px; padding: 10px;
    &:after { content: ""; display: block; }
}
.btn.icon_btn {
    position: relative; border: 0;
    width:40px; min-width: 40px; height: 40px; padding: 5px 5px;
    &:after { content: ""; display: block; }
}
.btn.btn_xs.icon_btn {
    position: relative; border: 0;
    width:30px; min-width: 30px; height: 30px; padding: 0px; 
    &:after { content: ""; display: block; }
}

    .btn.icon-scenario_add { 
        &:after { @include sp_icons($icon-scenario_add);}
    }
    .btn.icon-item_add { 
        &:after { @include sp_icons($icon-item_add);}
    }
    .btn.icon-item_edit { 
        &:after { @include sp_icons($icon-item_edit);}
    }
    .btn.icon-calendar { 
        &:after { @include sp_icons($icon-calender-w);}
    }




//이동버튼
.btnDisplace {display:inline-block; font-size:0px; @include sp_icons($btn_move);}

// 버튼 정열
.btn_bottom { margin-top: 40px;}
.btn_set { 
    &:after { content: ""; display: block; clear: both;}
    &.abs { position: absolute; left:0; right:0; z-index: 1;}
}
.btn_set-center { text-align: center; font-size: 0; line-height: 0;
    .btn { margin: 0 5px; }
}
.btn_set-single  { text-align: center;  padding: 0px;
    .btn_item { a , button, input { width: 100%;} }
}
.btn_set-double { padding: 0px;
    &::after { content: ""; display: block; clear: both;}
    .btn_item { position: relative; width: 50%; float: left;  
        a , button, input[type="button"]  { width: 100%;} }
    .btn_item:first-child { padding-right:5px; }
    .btn_item:last-child  { padding-left:5px; }/*  */
}
.btn_set-triple { padding: 0px;
    &::after { content: ""; display: block; clear: both;}
    .btn_item { position: relative; width: 33.3333%; padding: 0 2px; float: left; a { width: 100%;} }
    .btn_item:first-child { padding-left:0px; }
    .btn_item:last-child  { padding-right:0px; }
}

//좌우정렬
.btn_alignment { padding: 0px;
    &::after { content: ""; display: block; clear: both;}
    .btn.btn_lg { min-width: 135px !important;}
    .btn_item.fr { float:right; }
    .btn_item.fl { float:left;}
}

//우측정렬 
.btn_right { padding: 0px; text-align: right;
    &::after { content: ""; display: block; clear: both;}
    .btn_item { 
        display:inline-block; margin-left:5px;
        .btn.btn_lg { min-width: 135px !important;}
    }
}

//버튼정렬가운데
.btn_center {padding: 0px; text-align: center;
    &::after { content: ""; display: block; clear: both;}
    .btn_item { display:inline-block; margin: 0 4px;
        .btn.btn_lg { min-width: 135px !important;}
    }
}



/* 아이콘 버턴 */
.icon_btn.rd25 { position: relative; padding-left:25px;
    i { position: absolute; top:2px; left: 4px; }
}

/* 버튼 라운드 */
.btnRound { display:inline-block; background:#4d4d4d; border-radius: 100%; font-size:12px; color:#fff; min-width:25px; height:25px; line-height: 25px; text-align: center;
    i, span { background:#4d4d4d; overflow: hidden; display:inline-block; vertical-align: top; letter-spacing: 0px; font-weight: 300;}
    i { font-size:20px; line-height: 23px; border-radius: 100%; }
    span { padding:0 15px; border-radius: 50px;}
}


/* ====== PAGING ====== */    
.paging-area { width:100%; clear:both;position:relative; padding-top: 15px;  letter-spacing:0px; text-align:center;
    .paging { position:relative; text-align:center; }
    .paging a,
    .paging strong { position:relative; display:inline-block; color:#666; 
        min-width: 30px; height:30px; line-height:28px; margin: 0 1px;
        text-decoration:none; vertical-align:top; padding:0 10px;
        font-size: 14px; font-weight: 400;
        border:1px solid #c1c1c1; box-sizing: border-box;
        em { font-style: normal; }
        &:after {  }
    }
    .paging strong.on { background:#5c77fa; color:#fff; font-weight: 400; border:1px solid #5c77fa; }
    .paging a:hover { border-color:#b1b1b1;}
    .paging a:focus { border-color:#b1b1b1;}

    .paging a.prev, .paging a.prev_end, .paging a.next, .paging a.next_end {
        border:1px solid #dcdcdc; box-sizing: border-box;
        &:after { content:""; display: block; position: absolute; top:4px; left:4px; }
     }
    .paging a.prev { width: 30px;  padding:0px; margin-right: 10px; text-indent: -9999px;
        &:after { @include sp_icons($icon_page_prev);     } 
    }
    .paging a.prev_end { width: 30px;  padding:0px;  margin-right:5px; text-indent: -9999px;
        &:after { @include sp_icons($icon_page_prevprev); }
    }
    .paging a.prev:hover     {  /* &:after { @include sp_icons($icon_page_prev-on) };     */ }
    .paging a.prev_end:hover {  /* &:after { @include sp_icons($icon_page_prevprev-on) }; */ }

    .paging a.next { width: 30px;  padding:0px;  margin-left:10px; text-indent: -9999px;
        &:after { @include sp_icons($icon_page_next);     }
    }
    .paging a.next_end { width: 30px;  padding:0px;  margin-left:5px; text-indent: -9999px;
        &:after { @include sp_icons($icon_page_nextnext); }
    }
    .paging a.next:hover     {  /* &:after { @include sp_icons($icon_page_prev-on) };     */ }
    .paging a.next_end:hover {  /* &:after { @include sp_icons($icon_page_prevprev-on) }; */ }

   
}

/* ================== 팝업 레이아웃 */
/** //popup-wrap **/
.popup-dim { position: fixed;top: 0;left: 0;right: 0;bottom: 0; 
    width: 100%;height: 100%; background: #000000; opacity: 0.7;filter: alpha(opacity=70); z-index: 90000;
}
.trp.popupfixed-wrap { display: none;  position: absolute; top: 0;left: 0;
//    position: fixed;   /*table-layout: fixed; */
    width: 100%;  height: 100%;min-height: 100%; z-index: 99999;    
//    overflow-x: hidden;
//    overflow-y: scroll;
    -webkit-overflow-scrolling: touch; 
    .popup-dim { position: fixed;top: 0;left: 0;right: 0;bottom: 0;
        width: 100%;height: 100%; background-color: transparent; z-index: 0;
        /*background: #000000;opacity: 0.5;filter: alpha(opacity=50);*/}
    .popup-align    { display: table;*display: block; table-layout: fixed;  width: 100%; height: 100%; text-align: center; }
    .popup-vertical {
        display: table-cell;width: 100%;padding: 20px 0;
        text-align: center;vertical-align: middle;
    }
    .popup-layer {   position: relative; z-index: 1; display: inline-block;*display: inline;zoom: 1;}
    .btn_close { position: absolute;top: 0px;right: 0px; 
        width: auto; height: 67px; padding:22px 30px; margin: 0; 
        font-size: 0px; line-height: 1; color: transparent; box-sizing: border-box;
        &:after { content: ''; display: block; @include sp_icons($pop_close)}
    }
    .pop_tit {  padding: 22px 30px 17px; height: 67px;
        font-size: 24px; font-weight: normal; color:#fff;  line-height: 1.1;
        font-family: $subFT; background: $colorBGp;}
    .popup-layer.option {
       position: relative;top: 0px; /*width:100%;*/ min-width: 460px; height: auto;
       text-align: left;background: #fff;
    }
    .section {position: relative;padding: 30px;
    }
    .bottom_set { margin-top: 20px;}
}

