@charset "utf-8";

/* ====== PAGING ====== */    
.icon_page_prev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block;  @include sp_icons($icon_page_prev);  }
}   
.icon_page_prevprev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block; @include sp_icons($icon_page_prevprev); }
}   
.icon_page_prev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block; @include sp_icons($icon_page_next); }
}   
.icon_page_prev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block; @include sp_icons($icon_page_nextnext); }
}   


/*  reload */
.icon_relod { display: inline-block; width: 20px; height: 20px;
    position: relative;
    &:after { content: ""; display: block; @include sp_icons($icon-reload); }
}

