/* ================== 팝업 내용 */
.trp.popupfixed-wrap.popLayer { display: none;  position: absolute; top: 0;left: 0; width: 100%;  height: 100%; min-height: 100%; z-index: 90000; -webkit-overflow-scrolling: touch; 
    .section { position: relative; height: auto; text-align: left; background: #fff; border-radius: 3px;}
    .pop_tit_type2 { height: 70px; color:$colorD; font-size: 26px; line-height: 64px; letter-spacing: -2px; font-family: $subFT; text-align:center; font-weight: 700; margin-bottom: 40px;}
    .popSubTit { font-size:16px; font-weight: 500; color:$colorD; line-height:50px;}
    .btn_close2 { width: 100%; margin: 0;}

    .popup-layer { width: 500px; }
    .w580 { width: 580px;}
    .w620 { width: 620px;}
    .w670 { width: 670px;}
    .w820 { width: 820px;}

    //약관팝업
    .terms {
        .pop_tit_type2 { margin-bottom:0px;}
        .termsBox { border:1px solid #bfbfbf; height:410px; width: 100%; border-radius: 3px;
            .inner { display:block; margin:20px;  color:$colorL;}
        }
    }
    

    //마이페이지 안에 팝업
    .popCont { 
        table td { padding:10px 10px;}
        table td.js-table_checkbox { cursor: pointer; }
        .input-list > li {margin-bottom: 10px; }
        input[type="text"], input[type="password"] { color: #515151;
            height: 50px;  @include border-radius(3px); 
        }
        .input_icon {
            input[type="text"] + button {  top:10px;  }
        }

        //셀렉트, 파일첨부 높이 재정의
        .select2-container .select2-selection--single { height:50px;}
        .select2-container--default .select2-selection--single .select2-selection__rendered { line-height:50px;}
        .select2-container--default .select2-selection--single .select2-selection__arrow b { margin-top:-5px;}
        .trp.file-box {
            .trp-Filetext,
            .btn { height:50px;}
        }

        /* 게시판 겟수 보기 셀렉트 */
        .select-box.page_view{
            .select2-container .select2-selection--single { height:40px;}
            .select2-container--default .select2-selection--single .select2-selection__rendered { line-height:40px;}
            .select2-container--default .select2-selection--single .select2-selection__arrow b { margin-top:-10px;}
        }


        //시나리오 스텝 전체 리스트
        .table_body { max-height:330px;}

        //권한 안내
        .authorityBox { margin:-15px 25px 0 25px;
            dl { display: block; border-top:1px solid #d2d2d2; padding:15px 15px 15px 65px;
                dt { font-size:18px; color:#000; font-weight: 400;}
                dd { font-size:14px;
                    span { font-size:12px;}
                }
                &:nth-child(1) { background:url('/resources/images/myproject/icon_authority01.png') no-repeat 15px center;}
                &:nth-child(2) { background:url('/resources/images/myproject/icon_authority02.png') no-repeat 15px center;}
                &:nth-child(3) { background:url('/resources/images/myproject/icon_authority03.png') no-repeat 15px center;}
            }
            dl:first-child { border-top:0px;}
        }

        //케이스 전체보기
        .caseView {
             dl { clear: both; border-bottom:1px solid #929292; padding-bottom:20px; overflow:hidden;
                dt { float:left; width:16%; line-height:50px; font-weight: 500;
                    font-size: 18px;
                }
                dd { float:left; width:84%; line-height:50px; font-weight: 400;
                    color:$colorL;
                }
                //dd { float:right; width:84%;}
                
             }
        }

        //스케줄등록수정
        .scheduleList { clear: both; padding-bottom:20px; overflow:hidden; 
            position: relative; margin:0 20px;
            dt { position: absolute; left:0px; font-weight: 500; padding-top:14px;
                em { color:#ed0000; }
            }
            dd { display:block; margin-left:120px; margin-bottom:10px;}
            .none { padding-top:0px;}

            //검색창
            .input_icon.search input[type="text"] + button { top:10px;}
            // 달력
            .datepicker { clear: both; padding-bottom:7px;
                .datepicker-box { height:50px; width:210px;
                    &:after { top:13px !important;}
                }
            }
            .timepicker-area { clear: both; padding-bottom:7px;
                .timepicker-box { height:50px; width:210px;
                    &:after { top:13px !important;}
                }
                .trp-timepicker { top:50px;
                    .ui-count input { text-align: left; height: 40px;}
                }
            }
            .bar { margin: 0 7px;}
            .combo3 { @include clearfix;
                .combo_item{ float: left; width:46.6666%; }
                .combo_term{ float: left; width: 6.6666%; }
            }
        }

        //권한대상목록
        .authorityList { 
            dl { clear: both; border-bottom:1px solid #929292;padding-bottom:20px;
                overflow:hidden; 
                dt { float:left; width:23%; font-weight: 500;}
                dd { float:right; width:77%; display: inline-block;
                    position: relative; color:#999;  } 
                    .group_name { display: inline-block; margin:0 5px; color:#999;
                    //span { display: inline-block; vertical-align: middle;}
                }
                &.select { padding-bottom:5px;
                    dt { line-height:42px;}
                    .select-box { display:inline-block; height:45px; margin-left:10px;
                        min-width: 250px; 
                        .select2-container .select2-selection--single { height:45px;}
                        .select2-container--default .select2-selection--single{

                            .select2-selection__rendered { line-height:45px;}
                            .select2-selection__arrow b { margin-top:-7px;}
                        }
                    }
                }
            }
            /* 권한 리스트  */
            .authority-list { min-height: 230px; 
                li { border:0; }
                >li + li {  border-top: 1px solid #c1c1c1;  }
                li:hover { background-color: #ececec; z-index: 2000;}
                .ui-state-default, .ui-state-highlight { 
                    background-color: #fff; cursor: pointer;
                }
                .colitem{ @include ellipsis(); padding: 10px 5px;
                    font-size: 14px; font-weight: 400; text-align: center; color: #a0a0a0;
                }
            }
            

            .listBox { display: block; border:1px solid #c1c1c1; padding:0 20px 5px; margin-top:30px;
                .table_board thead tr:first-child th { border-top:0px;}
                .table_board tbody tr:last-child td {border-bottom:0px;}
                .table_body  { max-height: 230px;  }
                .table_bodyD  { max-height: 230px;  overflow-y: scroll;  }
                .hideCheck tr { 
                    input[type="checkbox"] { display: none;}
                    label { cursor: pointer; display: block;}
                 }
            }

            //권한대상추가2단
            .dualBox { display: block; margin-top:30px; overflow: hidden;
                .one, .two { width:255px; display:inline-block;}
                .displaceBtn { display: inline-block; margin:40px 9px 0;}
                .title { font-size:16px; font-weight: 400; padding-left:10px;}
                .listBox { margin-top:10px;}
            
            }
        }

        //알림테이블답변토글
        .notice_table-wrap{
            .table_body { height:auto; max-height:auto; }
            .answerTable { 
                .question { cursor: pointer;
                    .tit { text-align: left; position: relative;
                        &:after { content:''; position: absolute; right:10px; top:50%; margin-top:-6px;  @include sp_icons($accordion_arrow_down); opacity: 0.5;}
                    }
                    &.on { 
                        .tit:after {  @include sp_icons($accordion_arrow_up); opacity: 0.5;}
                    }
                }
                .answer { 
                    td { padding:0px; height:auto; border-bottom:0; }
                }
                .answerBox { display:none; padding:10px; text-align: left;
                     background:#f5f5f5; border-bottom: 1px solid #c1c1c1;
                    .cont { padding:20px; font-size:14px; line-height:1.5;
                        height:200px; 
                        .mark_dot-list li { font-size:inherit; color:inherit; 
                            line-height: inherit;
                            &:after { top:8px; left: 0; }
                        }
                    }
                    .answerBtn { border-top:1px solid #d1d1d1; padding:10px 0; display:block; text-align: right;
                        .btn { border-radius: 0px; color:#909090; border:1px solid #d9d9d9; }
                    }
                }
            }
        }// notice_talbe-wrap


    }

    /* 시나리오 스텝 전체 보기 */
    .table_board.type2 { width:100%; 
        td input { padding: 0px 15px ; height:30px; line-height:26px; border-radius: 0px; text-align:center; font-size:14px; border:1px solid #c1c1c1; }
    }

    /* 시나리오 레이어 // 테스트 결과 상세보기 */
    .view_layer { display: block; width: 100%; height: 100%; margin-top: 0;
        position: relative; top:0; left: 0;
        background-color: #fff;
        >.inner { padding: 20px 25px; 
            .col_left  { padding-right:8px; }
            .col_right { padding-left:8px; 
                .step_view-area {  padding-bottom: 10px;
                    >.inner { height: 327px; }
                }
            }
        }
        .tit-box { position: relative; padding:10px 0;
            .h3 { font-size: 20px; font-weight: 500; text-align: left;}
            .ab-r { top:0px;  }
        }
        .step_thumbnail-area{ position: relative; padding: 5px 20px 10px;
            .step-list{
                li {
                    .li-inner{ position: relative; display: inline-block;
                        font-size: 0; line-height: 0;
                        a { display: block;
                            img { width: 73px; height: 40px;}
                            &:after { content: ""; display: block; width: 100%; height: 100%;
                                position: absolute; top:0; left: 0;
                                border: 1px solid #c5c5c5; box-sizing: border-box;
                            }
                        }
                    }
                }
                li.current {
                    .li-inner{ a:after { border: 3px solid #375ac0; } }
                }
            }
            .bx-step_controls { position: relative; 
                >div {  position: absolute; top:-40px; width: 15px; height: 40px; }
                .bx-step_prev { left:-20px;}
                .bx-step_next { right:-20px;}
                a { display: block; width: 15px;height: 40px;
                    font-size:0; line-height: 0; background-color: #c5c5c5;
                    &:after { content: ""; display: block; 
                        position: absolute; top: 50%; left:50%; 
                        margin-top: -5px; margin-left: -3px;
                    }
                }
                a:hover { background-color: #757575; }
                .bx-prev { &:after { @include sp_icons($gallery_arrow_left_s);   } } 
                .bx-next { &:after { @include sp_icons($gallery_arrow_right_s);  } } 
            }
        }
        .step_view-area { margin-top: 5px;
            .inner{  border: 1px solid #c5c5c5; height: 282px; }
            .img { position: relative; 
                img { width: 100%;  box-sizing: border-box; }
            }
            .img.img_check { position: relative;
                background: url(/resources/images/myproject/data_check_bg.png) 0 0 repeat;
                img {   }
            }
        }
        .table-wrap {
            .btn_set { margin-top: 15px;
                .btn { width: 125px; }
            }
        }
    }

}

.searchLine { display:block; position: relative; border-bottom:1px solid #929292; padding-bottom:10px; 
    .searchInput { width: calc( 100% - 120px); }
    input[type="button"] { position: absolute; right:0px; top:0px; width:110px;}
}

