/* header.scss */
.member #header {  }
#header {   border-bottom: 1px solid #e7e7e7;
    
    background: url(/resources/images/common/header_bg.png)  0 0  repeat-y;
    background-size: 100% ;
    .inner { width:$G-width; margin: 0 auto; position: relative; }
    .logo { padding:  20px 0;  height: 91px;
        a { display: inline-block;  }
    }
    .util-list { position: absolute; top:40px; right:0;
        @include clearfix();
        li + li{ position: relative;
            &::before { content: ""; display: block; width: 1px; height: 17px;
                position: absolute; top:3px; left: -1px;
                background-color: #fff;}
        }
        li { float: left; padding: 0 15px;
            a {  display: inline-block; font-size: 15px; color:#fff; font-weight: 300;}
        }
        li:last-child { padding-right: 0; }
    }

    .sub-bar  { height: 50px; background-color: #242b45;
        .inner {}
    }
    .dep1-gnb { position: absolute; top:20px; right:0px; 
        .gnb-list{ @include clearfix();
            li { float: left; 
                a { display: block; padding: 15px 37px 28px;
                    font-size: 22px; font-weight: 500; color:#fff; @include opacity(0.5);
                }
            }
            li.on, li:hover { a { @include opacity(1); } }
            li:last-child { a { padding-right: 20px;} }
        }
    }
    /* 2뎁스 메뉴 */
    .dep2-gnb.on {display: inline-block;}
    .dep2-gnb { display: none; position: absolute; top:0px; right:0px; 
        .gnb-list{ @include clearfix();
            li { float: left;  padding: 0 20px;
                a { position: relative; display: block; padding: 16px 15px; height: 50px;
                    font-size: 15px; font-weight: 400; color:#fff; 
                    box-sizing: border-box;
                }
            }
            li:last-child { padding-right: 0px; a { padding-right: 20px;} }              
            li:nth-child(1).on, li:nth-child(1):hover { 
                a { background-color: #5c77fa;}                
            }
            li:nth-child(2).on, li:nth-child(2):hover { 
                a { background-color: #6375fa;}                
            }
            li:nth-child(3).on, li:nth-child(3):hover { 
                a { background-color: #6773fa;}                
            }
            li:nth-child(4).on, li:nth-child(4):hover { 
                a { background-color: #6a73fa;}                
            }
        }
    }

    /* 로그인 정보  */
    .login-info {
        .info-list { @include clearfix();
            li { position: relative; float: left; 
                /* &:after {content: ""; display: block; width: 1px; height:9px; 
                position: absolute; top:17px; left: 0;
                background-color:#bfbfbf; }
                &:first-child::after { display: none; } */
                a { position: relative; display: block;  padding:16px 2px; height: 50px; 
                    font-weight: 400; box-sizing: border-box; }
                .rd_new-box  { margin-top: -6px; margin-left: -18px; vertical-align: top; }
                .rd_new-box { display: inline-block; padding: 0px 5px; box-sizing: border-box; 
                    font-size: 8px; color:#ff0000;  height: 16px;  line-height: 16px;
                    border-radius: 8px; background-color: #fff;
                }
            }
            li:first-child { a { padding-left: 0px; } }
            .user_id { a {  font-size: 15px;  color:#ffffff; }}
            .user_alarm { a { 
                .icon_alarm{  display: inline-block; width: 30px; height: 30px; margin-top: -5px;
                    &:after { content: ""; display: block; @include sp_icons($icon-alarm);  }
                }
            }}
            .user_log {
                a { display: inline-block; margin-top: 12px; margin-left: 20px; padding:  0 15px;  height: auto;
                    font-size: 12px; color:#848894; line-height: 21px;
                    border: 2px solid #848894; @include border-radius(13px); box-sizing: border-box;
                }
            }
        }
    }
}