/* footer.scss */
#footer{ background-color: #3f3f3f;
    .inner { position: relative; width:$G-width; margin: 0 auto; padding: 30px 0; }
    .col-r { position: absolute; top:30px; right: 0;  text-align: right; }
    .copyright { display: inline-block; margin-top: 20px; font-size: 12px; color:#797878;  font-weight:400;  }
    .link_set { margin-top: 20px;
        .link_item { position: relative;  padding: 0 10px;
            font-size: 12px; color:#797878; font-weight: 400; }
        .link_item + .link_item { &:after { content: ""; display: block; width: 1px; height:14px;
            position: absolute; top:3px; left: -1px; 
            background-color: #797878;
        }}
        .btn_item { display: inline-block; padding: 9px 25px 8px; 
            font-size: 12px; color:#797878; font-weight: 300;
            border: 1px solid #797979;
        }
    }

    .scroll_top {  display: none;
        position: absolute; bottom: 50px; left: 50%;  width: 55px; height: 55px; 
        margin: 0 0 0 540px; 
        cursor: pointer; z-index: 100;
        a { position: relative; display: block; width: 55px; height: 55px; 
            background-color: #fff; border-radius: 50%; 
            font-size: 12px; font-weight: bold; color:#000;
            border:1px solid #efefef;
            box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.05);
            &:after { content: "＾"; display: inline-block; position: absolute; top:10px; left: 23px;
                /* @include sp_icons($icon_scroll_top); */ }
            span { display: block; position: absolute; top:22px; width: 100%; text-align: center; }
        }
        @media screen and (max-width: $G-wMobile ) {
            left: auto;  right:10px;
            margin: 0;
        }
    }

}
