#header.member_login            {  } // 로그인
#header.member_emailid          {  } // 이메일찾기
#header.member_pass             {  } // 비밀번호재설정
#header.member_membership       {  } // 회원가입
#header.member_authentication   {  } // 인증하기
#header.member_user_info        {  } // 사용자정보

.wrapper.member { 
    input[type="text"], input[type="password"] { color: #989898;
        width: 100%; height: 50px; padding: 8px 15px 8px;
        @include border-radius(3px); 
    }
    /* 유효성 채크 */
    .check_validity { position: relative; display: block;
        input[type="text"], input[type="password"] { padding-right:50px; 
            &+ i { position: absolute; top:10px; right:10px;
                display: block;  width: 30px; height: 30px; } 
        }
        .input_error  { 
            &+ i:after { content: ""; display: block; @include sp_form($input_error); }
        }
        .input_success {
            &+ i:after {  content: ""; display: block; @include sp_form($input_success); }
        }
    }

    .login-inner { width: 600px; margin: 0 auto; padding: 30px 79px 39px; margin-top:40px; 
        .title  { position: relative; padding: 0px 0 50px;
            .h1 { text-align: center; font-size:26px;}
            .t_desc { position: absolute; top:38px; left: 0;  width: 100%; 
                font-size: 16px; font-weight: 400; color:#9a9a9a; text-align: center; line-height: 1.4;
            } 
        }
        .input-list {
            >li { position: relative; margin-bottom: 10px;}
            .label { display: inline-block; margin-left: 5px; vertical-align: top; line-height: 1.3; color:$colorL;}
            .label a { border-bottom: 1px solid #c5c4c5;  color:$colorL;}
        }

        /* 간편 로그인 */
        .simple-box {  position: relative; margin-bottom: 15px; text-align: center; 
            background: url(/resources/images/common/dot_bfbfbf.png) 0 12px  repeat-x;
            .simple_login {  padding: 0 10px; display: inline-block; 
                font-size: 13px; font-weight: 500; color:$colorL;
                background-color: #f2f3f7; z-index: 1;
            }
        }
        .btn.bg_sns-facebook { height: 60px;
            background: #fff url(/resources/images/common/btn_bg-facebook.png) center center no-repeat; }
        .btn.bg_sns-google   {  height: 60px;
            background: #fff url(/resources/images/common/btn_bg-google.png) center center no-repeat; }

        .guide-msg  { color:$colorL; }
        /* 가입완료 */
        .join-img { padding: 50px 0 65px; text-align: center;
            img { display: inline-block;}
        }
    }

    /* free_member  */
    .free_member { position: relative; padding-left: 28px;
        font-size: 16px; font-weight: 400; color:#f3720a;
        a { border-bottom: 1px solid #f3720a;  }
        &::before { content: ""; display: block; width: 24px; height: 24px;
            position: absolute; top:0; left: 0;
            @include sp_icons($icon-freemember);
        }
    }
}