/* style_common.scss */

html,body { height: 100%; }
.wrapper { position: relative; min-height: 100%;  width: 100%;  background-color: #f2f3f7; display: inline-table; }
#container {  } 
#contents  { min-width:$G-width; padding: 50px 0 120px; }
.body-inner { position: relative; width:$G-width; margin: 0 auto;}
.last-section { margin-bottom: 100px;}


/* ========================== text */
.h1 { font-size: 36px; font-weight: 500;   color:$colorD; }  // 적용(member/title)
.h2 { font-size: 26px; font-weight: 500;   color:$colorD; }  // 적용(myproject/title)
.h3 { font-size: 23px; font-weight: bold;   color:$colorB; } 
.h4 { font-size: 20px; font-weight: bold;   color:$colorB; } 
.h5 { font-size: 17px; font-weight: bold;   color:$colorB; } 
.h6 { font-size: 15px; font-weight: normal; color:$colorB; } 
.h1, .h2, .h3, .h4, .h5 {
    em { color:$colorP; }
}

.text-muted     { display: block;
    font-size:14px; color:$colorL; font-weight: normal;  
    line-height: 1.25; letter-spacing: -0.5px; }
.text-notice    { display: block;
    font-size:16px; font-weight: 300;  color:$colorL;
    line-height: 1.25; letter-spacing: -0.5px; font-style: normal;}
.colorB { color: $colorB; } // 강조진하게
.colorL { color: $colorL; } // 저체도
.colorE { color: $colorE; } // 에러

.main_title { position: relative; }
.sub_title { position: relative;  margin-bottom: 10px; text-align: left;
    .h2 { text-align: left; display: inline-block;
        em.name { color:#1C4BB7; }
    }
    .date { font-weight: 400; }
    .rd25.ab-r { position: absolute; top:5px; right:0;}
    .trp.ab-r { position: absolute;  top:10px; right:0;
        label {font-size: 14px; color:#949494;}
    }
    .tit_desc{ margin-left: 10px; font-size: 14px;  color:#949494; font-weight: 400; }
    .tit_count { position: relative; display: inline-block; vertical-align: middle;
        margin-left: 15px; padding-left: 15px;
        &:after { content: ""; display: block; width: 1px; height: 11px;
            position: absolute; top:15px; left: -1px;
            background-color: #d2d2d2;   }
        font-size: 26px; font-weight: 400; color: #464646;
        em { font-size: inherit; font-weight: inherit; color: inherit;}
    }
    a { display: inline-block; }
    a:hover { text-decoration: underline; }
}
.con_title { position: relative; margin-bottom: 15px;
    em { color:$colorE; }
}
.side_area { position: absolute; bottom:0px; right:0; }

// 그림자 박스 
.article-box { background-color: #fff; border: 1px solid #d2d2d2;
    box-sizing: border-box;
     /* @include shadow(0px, 0px, 15px, 0.05);  */ }
.article-box + .article-box { margin-top: 10px; }
.article-box {
    .table-wrap.data_table-area { padding: 15px 10px 34px;}
    .table-wrap.board_table-area { padding: 15px 25px 34px;}
    .table_btn-info { padding: 20px 25px 0px;
        .btn_set { 
            .fl, .fr { font-size: 0; line-height: 0; 
                .trp  { vertical-align: middle; margin-right: 20px; 
                    font-size: 14px; line-height: 1.4; 
                }
            }
        }
    }
    .table_btn-info + .table-wrap.board_table-area { padding: 10px 25px 34px;}
    .search-wrap { padding: 20px 25px 30px;}
    .calendar-wrap { padding: 30px;}
    .total_graph-wrap {padding: 30px;}
}
article + article { .sub_title { margin-top: 30px;} }


/* ==========================  안내 문구  */
.guide-msg { position: relative; padding-left:22px; 
    font-size: 14px; font-weight: 300; letter-spacing: -0.4px; 
    @include wordBreak-word();
    &::before { content: ""; display: block; position: absolute; top:2px; left: 0;
        @include sp_icons($icon-guide_msg);
    }   
 }


// 라운드 박스  new
.rd25.gray_w, .rd21.gray_w, .rd17.gray_w { display: inline-block; text-align: center;
     box-sizing: border-box; background-color: #a5a5a5; color:#fff;  }
.rd25.gray, .rd21.gray, .rd17.gray { display: inline-block; text-align: center; 
    box-sizing: border-box; background-color: #858585; color:#fff;  }
.rd25.dark, .rd21.dark, .rd17.dark {  display: inline-block; text-align: center;
    box-sizing: border-box; background-color: #3c3c3c; color:#fff;  }
.rd25.purple, .rd21.purple, .rd17.purple {  display: inline-block; text-align: center;
    box-sizing: border-box; background-color: #836afa; color:#fff;  }
.rd25.blue, .rd21.blue, .rd17.blue {  display: inline-block; text-align: center;
    box-sizing: border-box; background-color: #158ef7; color:#fff;  }
.rd25.orange, .rd21.orange, .rd17.orange {  display: inline-block; text-align: center;
    box-sizing: border-box; background-color: #f95754; color:#fff;  }

.rd25 { font-size: 14px; padding: 0px 12px; height: 25px; font-weight: 400; line-height: 25px; border-radius: 13px;  }
.rd21 { min-width: 39px; font-size: 14px; padding: 0px 10px; font-weight: 400; height: 21px; line-height: 21px; border-radius: 11px; }
.rd17 { min-width: 31px; font-size: 12px; padding: 0px  8px; font-weight: 400; height: 17px; line-height: 17px; border-radius:  9px; }

.rd25.g_date.on, .rd21.g_date.on, .rd17.g_date.on {  display: inline-block; box-sizing: border-box;
    background-color: #949494; color:#fff;  }
.rd25.g_date, .rd21.g_date, .rd17.g_date {  display: inline-block; box-sizing: border-box;
    background-color: #d5d5d5; color:#fff;  }

/* 결과  */
.t_state {
    &.state0 { color: $colorD;   }
    &.state1 { color: #ed0000; }
    &.state2 { color: #00ca09; }
}

/* ========================== typo items*/
.pro-items{
    >span { position: relative; display: inline-block; font-size: 14px; color:#949494}
    >span + span { &::before { content: " > "; display: inline; }  } 
}
.other-items{
    >span { position: relative; display: inline-block; 
        font-size: 14px; color:#949494; }
    >span + span {  margin-left: 12px; padding-left: 12px;
        &::before { content: ""; display: block; width: 1px; height:10px;
            position: absolute; top:5px; left: -1px;
        background-color: #a5a5a5;
    }} 
}


/* ========================== typo list */
.mark_char_item { position: relative; padding-left: 0.8em; text-indent: -0.8em;  }
.mark_char-list {
    li { position: relative; padding-left: 0.8em; text-indent: -0.8em;  }
}

.mark_item { position: relative; padding-left: 0.1em; text-indent: -0.1em;  }
.mark-list {
    li { position: relative; padding-left: 1em; text-indent: -1em;  }
}
.mark_dot-list{
    li { position: relative; 
        padding-left: 0.8em; 
        &:after { content: ""; display: block; width: 5px; height: 5px; 
            @include border-radius(100%);
            background-color:#898989;
            position: absolute; top:12px; left: 0; 
        }
    }   
}
.info-list { text-align: left;
    dt, dd { display: inline-block; font-size: 16px;}
    dt { position: relative; padding-left: 15px;
        @include list-div_bar(8px, -1px, 10px, #bfbfbf);
        &:after {content: ": "; display: inline-block; font-size: inherit; color:inherit; }
    }
    dt:first-child{
        &::before { display: none; }
    }
    dd {  padding-right:  15px } 
}
.contextmenu_set { display: none; position: fixed; z-index:99;  
    border:1px solid #707070; background-color: #fff; @include shadow( 0, 0, 5, 0.01);
    button { width:100%; line-height: 30px; font-weight: 400; }
    .copy_btn  {}
    .paste_btn {}
}




/* ========================== contentBox */
/* ========================== //contentBox */


/* ========================== form list */
.form_list-wrap {
    .form_name { padding: 12px 0; width: 100%;
        font-size: 16px; color:#3d3d3d; font-weight: 500; text-align: left;
        border-top:1px solid #707070; border-bottom:1px solid #707070; 
    }
    .form-list { width: 100%; 
        li { padding: 5px 0;}
        .label { display: block; padding: 10px 0; text-align: left;
            font-size: 14px; color:#666666; font-weight: 400; }
        input, .textarea-box { @include border-radius(0); }
    }

}
/* ========================== //form list */


/* ========================== table_style default */
.table-info { padding: 20px 10px 0px;
    .info_dep1-list { @include clearfix();
        > li { position: relative; float: left; padding: 0 15px; vertical-align: top;
            font-size: 16px; font-weight: bold;
            a:hover { span { text-decoration: underline; } }
            span { margin-left: 3px;}
        }
        > li + li {  
            &:after { content: ""; display: block; width: 1px; height: 11px; 
                background-color: #bfbfbf;
                position: absolute; top: 7px; left:-1px; 
            }
        }
    }
    .info_dep2-list { @include clearfix(); 
        display: inline-block; vertical-align: top; margin-top: 3px;
        > li { float: left;  padding-left: 10px; font-size: 14px; }
        span { margin-left: 3px;}
    }
}


.table-wrap{ position: relative;}
table {  width: 100%; table-layout:fixed; 
    th, td { 
        a:hover { text-decoration: underline; } 
    }
    th.vet, td.vet { vertical-align: top;   }
    th.vat, td.vat { vertical-align: top;   }
    th.tal, td.tal { text-align: left;   }
    th.tar, td.tar { text-align: right;  }
    th.tac, td.tac { text-align: center; }
    th.bdl, td.bdl {  border-left:  $bolderLine !important; }
    th.bdr, td.bdr {  border-right: $bolderLine !important; }
    th.bdt_b, td.bdt_b {  border-top: $bolderBoldLine !important; }
    td.data-empty { text-align: center; border: 0;  }
}
table.top_bold_bar { border-top: $bolderBoldLine !important; }
table.topbar_none  { border-top: 0px !important;};
table .topbar_none  { border-top: 0px !important;};
table.bottom_bar   { border-bottom: $bolderLine !important; }


.table_list , .table_board {
    th.col_sot {
        background-image: url(data:image/gif;base64,R0lGODlhFQAJAIAAACMtMP///yH5BAEAAAEALAAAAAAVAAkAAAIXjI+AywnaYnhUMoqt3gZXPmVg94yJVQAAOw==);
        background-position: center right;
        background-repeat: no-repeat;
        cursor: pointer;
        white-space: normal;
        padding-right: 20px;
    }
    th.col_sot.up   {
        background-image: url(data:image/gif;base64,R0lGODlhFQAEAIAAACMtMP///yH5BAEAAAEALAAAAAAVAAQAAAINjI8Bya2wnINUMopZAQA7);
        //border-bottom: #000 2px solid
    }
    th.col_sot.down {
        background-image: url(data:image/gif;base64,R0lGODlhFQAEAIAAACMtMP///yH5BAEAAAEALAAAAAAVAAQAAAINjB+gC+jP2ptn0WskLQA7);
        //border-bottom: #000 2px solid
    }
    td {
        /* 진행률 */
        .i_progress { display: inline-block; width:15px; height: 15px; 
            &:after{ content: ""; display: block; width:15px; height: 15px; margin:2px 0 0 5px;
                @include border-radius(8px); background-color: #dbdbdb;
            }
            &.step0 {  &:after{ background-color: #dbdbdb;} }
            &.step1 {  &:after{ background-color: #ed0000;} }
            &.step2 {  &:after{ background-color: #33c32f;} }
        }

        /* 결과  */
        .t_state {
            &.step0 { color: $colorD;   }
            &.step1 { color: #ed0000; }
            &.step2 { color: #00ca09; }
            &.step3 { color: #188efa; }
        }
    }
}

/* ========================== table_list : .style1 */
.table_list{ width: 100%; border-top: $bolderBoldLine; 
    th { padding: 15px 20px;  text-align: center; 
        border-bottom: $bolderLine;  
        color:$colorB; background-color: #f7f8f8;         
    }
    td {  padding: 15px 20px; text-align: center;
        border-bottom: $bolderLine;  
        .td-inner { height: 46px; }
        .td-inner.oneline { padding-top: 13px; }
    }  
    th.tal, td.tal { text-align: left; }
    th.tar, td.tar { text-align: right; }
    th.tac, td.tac { text-align: center; }
    tbody tr:first-child td { border-top: 0;}
}

.table_board.JPadding > tbody > tr > td.check_num, .table_board.JPadding > tbody > tr > th.check_num { 
    padding-left: 20px !important; 
}
.table_board{ width: 100%; min-width: 100%; max-width: 100%;
    th { padding: 13px 10px 14px;  text-align: center; 
        font-size:16px; color:$colorD;  font-weight: bold; 
        .iconNotice { margin-top: 0; font-size: 16px; color:$colorD; font-weight: bold;  }
    }
    td {  padding: 10px 10px 10px; font-size: 14px;  font-weight: 400; text-align: center;
        color:#a0a0a0;
        border-bottom: $bolderLine;  
        @include ellipsis();

        .rd_new-box  { margin-top: -2px; margin-left: 0px; vertical-align: top; }
        .rd_new-box { display: inline-block; padding: 0px 5px; box-sizing: border-box; 
            font-size: 8px; color:#fff;  height: 12px;  line-height: 12px;
            border-radius: 8px; background-color: #ff0000;
        }
    }    
    td.check_num, th.check_num { padding-left: 20px; color:#666; em{ color:inherit}  text-align: left;  }
    td.check_num_s, th.check_num_s { padding-left: 10px; color:#666; em{ color:inherit}  text-align: left;  }
    td.num, th.num {  color:#666; em{ color:inherit}  }
    th.tal, td.tal { text-align: left; }
    th.tar, td.tar { text-align: right; }
    th.tac, td.tac { text-align: center; }
    thead tr:first-child th { border-top: $bolderBoldLine; }
    thead tr:last-child th  { border-bottom: $bolderBoldLine; }
    tbody tr:last-child td  { border-bottom: $bolderBoldLine;}
    tr.active { td { background-color: #ececec;}}
}

.table-wrap.table_scroll{
    .table_board tbody tr:last-child td {
        border-bottom: 0px solid #707070;
    }
    .table_board tbody tr:last-child td.data-empty {
        border-bottom: 1px solid #707070;
    }
    .table_body {
        border-bottom: 1px solid #707070;
    }
   
}
.table_board.type2 { width:100%; 
    td { padding: 5px 10px; height: 40px; }
    td input { padding: 0px 15px ; height:30px; line-height:26px; border-radius: 0px; text-align:center; font-size:14px; border:1px solid #c1c1c1; }
    .scenario_add { padding: 90px 10px;
        text-align: center;
        span { display: inline-block; margin-right: 20px;
            font-size: 22px; font-weight: 500; line-height: 50px; color:#000; 
        }
        .btn { vertical-align: top;}
    }
}

/* ==========================  table 상단 버튼영역 : style1 */
.tableInfoTop { width:100%; display: inline-block; margin-bottom:10px;
    .fl { float:left;}
    .fr { float:right;}
}

/* ==========================  form table : .style1 */
.form_table {  border-top: $bolderBoldLine; }
.form_table th { background: #f6f6f6; text-align: left; font-weight: bold; color:#797979}
.form_table th,
.form_table td { padding:6px 10px; border-bottom:1px solid #e6e6e6; height:30px;
    .textarea-box { position: relative; 
        textarea { height: 70px;}
    }
    .edit_module { height: 150px; overflow-y: auto;}
    .form_alert { display: none; margin-top: 5px; color: #ea0530; font-size: 12px; }
}
.form_table th i { color:#ea0530; margin-left:3px;}
.form_table td label { margin-right:30px;}
.form_table td input,
.form_table td a { vertical-align: middle;}
.form_table tr.disabled {
    th, td { color:#aaaaaa;}
}

/* 작은 폼  */
.form_table.type_s { border-top:1px solid #d6d6d6;  }
.form_table.type_s th { background: #f6f6f6; text-align: left; font-weight: bold; color:#797979}
.form_table.type_s th,
.form_table.type_s td { padding:6px 5px; border-bottom:1px solid #d6d6d6; height:30px;
    text-align: left; 
    .textarea-box { position: relative; 
        textarea { height: 70px;}
    }
    .edit_module { height: 150px; overflow-y: auto;}
    .form_alert { display: none; margin-top: 5px; color: #ea0530; font-size: 12px; }
}
.form_table.type_s tr:last-child td { border-bottom: 0px;}
.form_table.type_s th i { color:#ea0530; margin-left:3px;}
.form_table.type_s td label { margin-right:30px;}
.form_table.type_s td input,
.form_table.type_s td a { vertical-align: middle;}
.form_table.type_s td input[type="text"] {padding: 0px 15px; height: 28px;
    line-height: 26px; text-align: center; font-size: 14px;
    border-radius: 0px;
    border: 1px solid #c1c1c1;}
.form_table.type_s tr.disabled {
    th, td { color:#aaaaaa;}
}





/* ==========================  data table : .style1 */
.data_table { width:100%; border-bottom: $bolderBoldLine;   
    thead th { border-bottom: $bolderBoldLine;   }
}
.data_table th { font-size: 16px; color:$colorD; font-weight: bold; 
    .reservation { font-size: 12px; } // 예약
}
.data_table th,
.data_table td { text-align: center; padding:10px 10px; min-height: 40px; }
.data_table td { border-top: $bolderLine; 
    font-size: 14px; font-weight: 400; color:#959595; 
    strong  { font-size: 14px; font-weight:500; color:#666666;}
    .reservation { }
}
.data_table thead tr:last-child th { }
.data_table tbody tr:last-child td { }



// 데이터없음.
.data-empty{ 
    padding: 30px 0 30px !important;  border-top: $bolderLine;  border-bottom: $bolderLine; 
    text-align: center;
    .txt_empty {
        font-size: 14px; font-weight: 400; text-align: center; color: #a0a0a0;
    }
}
.mCustomScrollBox { padding-right:0px;} 
.mCustomScrollbar.pr0 > .mCustomScrollBox {padding-right:0px;}


/* ========================== 약관 내용 */
.terms_contents { padding: 0; 

	.h1 { font-size: 13px; color:$colorD; margin-bottom:5px;}
	.h2 { font-size: 13px; color:$colorD; margin-bottom:5px;}
	.h3 { font-size: 13px; color:$colorD;}
	
	p { margin-bottom: 10px; font-size: 14px;font-weight: normal; line-height: 1.5;
        strong{ color:$colorB; }
    }
    ul { li {line-height: 1.5; font-size: 14px;}} 
	ol { padding: 0 0 10px 0px;
		li { text-indent: -18px;padding-left: 18px; font-size: 14px; line-height: 1.5;}
	}
    ul.indent-list { padding: 0 0 10px 0px;
		li { text-indent: -18px;padding-left: 18px; font-size: 14px; line-height: 1.5;}
	}
	mark, .mark { color:#5aafe6; background-color: transparent;}
    
    /* ========================== table_terms */
    .table_terms {  width:100%;
        >thead >tr >th { border:1px solid #e1e1e1;padding: 5px 10px; font-size:14px; font-weight: 700; color:$colorB; text-align: center; background: #f8f8f8;
        }
        >tbody >tr {
            >th { border:1px solid #e1e1e1;padding: 5px 10px; font-size:14px; font-weight: 700; color:$colorB; text-align: center; background: #f8f8f8;
            }
            >td {
                border: 1px solid #e1e1e1; padding: 5px 10px; font-size:14px; color:$colorB; 
                text-align: center;
                a {font-size:14px; }
                .font_red { color:$colorE; }    
                li { font-size: 14px; }
            }
            &.rowspan >td{
                &:first-child {border-left: 1px solid #e1e1e1;}
            }
        }
    }
}



