/* ======================== input text  */
/* placeholder */
// ie
input[type="text"].placeholdersjs,input[type="password"].placeholdersjs,
textarea.placeholdersjs { color: $colorL; }
// Internet Explorer 10 이상에 적용
input[type="text"]:-ms-input-placeholder      ,input[type="password"]:-ms-input-placeholder      ,
textarea:-ms-input-placeholder{ color: $colorL; }
//  Webkit, Blink, Edge에 적용
input[type="text"]::-webkit-input-placeholder ,input[type="password"]::-webkit-input-placeholder ,
textarea::-webkit-input-placeholder    { color: $colorL; } 
// Mozilla Firefox 4 부터 18에 적용
input[type="text"]::-moz-placeholder   ,input[type="password"]::-moz-placeholder          ,
textarea::-moz-placeholder             { color: $colorL; } 

// input style
em.star { display: inline-block; height: 14px; padding: 0 5px; color: #f24848; font-weight: bold; }
label { color: #989898; }
input[type="text"], input[type="password"] {
    width: 100%; height: $formH; padding: 8px 15px 8px;
    font-size: 16px; vertical-align: middle;
    border: 1px solid $lineC_d; /* outline: none; */
    @include border-radius(3px); 
    -webkit-appearance: none;
    -moz-appearance: none;
}
input.h36[type="text"], input.h36[type="password"] {
    height:36px;
}


/* ======================== focus, ,disabled, input_error, input_success */
input[type="text"]:focus,input[type="password"]:focus               { border-color: $lineC_f; }
input[type="text"]:disabled,input[type="password"]:disabled         { background  : $colorBGd; }
input[type="text"].input_error,input[type="password"].input_error   { 
    border-style: dashed;
    border-color: $lineC_e; }
input[type="text"].input_success,input[type="password"].input_success   { 
    border-color: $lineC_s; }
/* 휴대전화번호 인증 */

    
/* 유효성 채크 */
.check_validity { position: relative; display: block;
    input[type="text"], input[type="password"] { padding-right:50px; 
        &+ i { position: absolute; top:5px; right:10px;
            display: block;  width: 30px; height: 30px; } 
    }
    .input_error  { 
        &+ i:after { content: ""; display: block; @include sp_form($input_error); }
    }
    .input_success {
        &+ i:after {  content: ""; display: block; @include sp_form($input_success); }
    }
}


/* 검색 */
.input_icon{  position: relative; display: block;
    input[type="text"] { padding-right:40px; 
        &+ button { position: absolute; top:5px; right:10px;
            display: block;  width: 30px; height: 30px;  font-size:0px; overflow: hidden;
            &:after { content: ""; display: block; @include sp_form($input_search); }
        }
    }
    &.search {
        input[type="text"] + button {
            &:after { content: ""; display: block; @include sp_form($input_search); }
        }
    }
    &.del { 
        input[type="text"] + button { 
            &:after { content: ""; display: block; @include sp_form($input_delect); }
        }
        input + button { display: none; }
        input[value=""]{ display: block; }
    }
    
}

/* 휴대전화번호 (선택) */
.check_tel { display: inline-block; width: calc(100% - 90px); 
    input { border-style: dashed;}
}
.check_tel + button { position: absolute; top:0; right:0; }
/* 휴대폰 인증  */
.phone_certification { display: none;
    .check_tel {  position: relative; }
    input { padding-right: 60px; }
    em { position: absolute; top:13px; right:15px; color:$colorE; }
}


/* member_sns */
.member_sns{ display: block; 
    input { background-color: #f5f5f5; padding-right: 70px !important; color:#515151 !important;}
    i { position: relative; width: 40px; height: 40px; 
        position: absolute; top:6px; right: 10px;
    }
    i::before { content: "";  display: block; width: 1px; height: 24px; 
        position: absolute; top:8px; left: -10px;
        background-color: $lineC_d; }
    i:after {  content: ""; display: block; @include sp_icons($sns-google); }
}



.input-box{ position: relative;  vertical-align: top;
    .check_byte-w { padding-right:  90px !important; }
    .check_byte-h { padding-bottom: 50px !important; }
    label, .dash  { display: inline-block; 
        padding: 8px 20px 8px ; box-sizing: border-box; }
    label:frist-child { padding-left: 0;  }
    .dash { padding: 8px 0px 8px 0px; } 
    input[type="text"], input[type="password"] {
       padding: 8px 15px 8px ;
    }
    .btn { margin-top:-1px;}
    .byte_check { position: absolute; top:10px; right:15px;
        em   { vertical-align: middle; font-size:14px;  color: #e90000; }    
        span { vertical-align: middle;  font-size:14px; }    
    }
}
.error-msg { display: none; font-size: 16px; font-weight: 300; color:$colorE;}


/* ======================== textarea  */
.textarea-box { border: 1px solid #bfbfbf; padding: 8px 13px 8px; border-radius: 3px;
    textarea { width: 100%;  height: 100px; border: 0; 
        -webkit-appearance: none;
        -moz-appearance: none;
        &.w240 { height:240px;}
    }
    .byte_check { margin-top:20px; height: 30px; text-align: right; 
        em   { vertical-align: bottom; font-size:14px;  color: #e90000; }
        span { vertical-align: bottom; font-size:14px; }    
    }
    
}


/* ========================  input radio */
.trp.radio-box { position: relative; display: inline-block; /* padding-left: 27px;  */ min-height: 20px;
    label { display: inline-block; height: 20px; margin-left: 5px; }
    em { display: inline-block; height: 20px;    text-indent: 0px; }
    input[type="radio"] { position: absolute; top:0; left: 0; /*  width:100%; */ width: 20px; height: 20px; margin: 0; padding: 0;  opacity: 0; filter: alpha(opacity=0);  }    
    input[type="radio"] + i { display: inline-block; vertical-align: top;  }
    input[type="radio"] + i:after { display: block; content: '';  @include sp_form($radio-off); }
    input[type="radio"]:checked + i:after {  @include sp_form($radio-on); }
    input[type="radio"]:disabled + i      {   @include opacity(0.5);
        &:after { display: block; content: '';  /* @include sp_form($radio-dim); */ }
    }
    input[type="radio"]:disabled + i +label  {  @include opacity(0.5);/*  color:$colorL; */ }
}
.trp.radio-box.re { 
    label { display: inline-block; height: 20px; vertical-align: middle;  text-indent: 0px; }
    input[type="radio"] + i { position: relative;  }
}


/* ========================  input checkbox */
.trp.checkbox-box { position: relative; display: inline-block;  min-height: 20px;
    label { display: inline-block; line-height: 20px; margin-left: 3px;  vertical-align: top;  }
    em { display: inline-block; height: 20px;     text-indent: 0px;}
    input[type="checkbox"] { position: absolute; top:0; left: 0;  /*  width:100%; */ width: 20px; height: 20px; margin: 0; padding: 0;  opacity: 0; filter: alpha(opacity=0); z-index: 1; }    
    input[type="checkbox"] + i { display: inline-block; vertical-align: top;  }
    input[type="checkbox"] + i:after { display: block; content: '';  @include sp_form($checkbox-off); }
    input[type="checkbox"]:checked + i:after {  @include sp_form($checkbox-on); }
    input[type="checkbox"]:disabled + i      {  @include opacity(0.5);
        &:after { display: block; content: '';  /* @include sp_form($checkbox-dim); */ }
    }
    input[type="checkbox"]:disabled + i +label  { @include opacity(0.5);  /* color:$colorL; */ }
}
.trp.checkbox_s-box { position: relative; display: inline-block;  min-height:18px;
    label { display: inline-block; line-height:18px; margin-left: 3px;  vertical-align: top;  }
    em { display: inline-block; height:18px;     text-indent: 0px;}
    input[type="checkbox"] { position: absolute; top:0; left: 0;  /*  width:100%; */ width:18px; height:18px; margin: 0; padding: 0;  opacity: 0; filter: alpha(opacity=0); z-index: 1; }    
    input[type="checkbox"] + i { display: inline-block; vertical-align: top;  }
    input[type="checkbox"] + i:after { display: block; content: '';  @include sp_form($checkbox_s-off); }
    input[type="checkbox"]:checked + i:after {  @include sp_form($checkbox_s-on); }
    input[type="checkbox"]:disabled + i      {  @include opacity(0.5);
        &:after { display: block; content: '';  /* @include sp_form($checkbox-dim); */ }
    }
    input[type="checkbox"]:disabled + i +label  { @include opacity(0.5);  /* color:$colorL; */ }
}
.trp.checkbox-box.re {
    label { display: inline-block; height:20px; text-indent: 0px; }
    input[type="checkbox"] + i { position: relative;  }
}


 
/* ========================  select */
.select-box { 
    .select2Basic, 
    .select2Basic_pop { width: 100%; height: $formH; }
    .select2Basic_s { width: 100%; height: 30px; }
}
.select2Basic, 
.select2Basic_pop { width: 100%;  min-width: 100%; /*50px;*/ }
.select2Basic_s   { width: 100%;  min-width: 100%; }

.select2-container {/*  height: $formH; */  text-align: left; }
.select2-container .select2-selection--single { height: $formH;}
.select2-container--default .select2-selection--single .select2-selection__arrow {right:10px;}
.select2-results__option { padding:0 15px;}
.select2-container--default .select2-selection--single {
    border: 1px solid $lineC_d;;
    border-radius: 0px; } 
.select2-container--default .select2-selection--single .select2-selection__rendered { line-height: $formH - 2px; padding: 0 30px 0 15px;  }
.select2-container--default .select2-selection--single .select2-selection__arrow { height: $formH - 2px; width: 30px; }
.select2-container--default .select2-selection--single .select2-selection__arrow b {
    background-color: transparent; border: 0; width: 23px; height: 23px;  margin-top: -9px; margin-left: -8px;
    &:after { content: ""; display: block; position: absolute; top:0; left: 0;
        @include sp_form($icon_select_arrow);
    }
}
.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);}

.select2-dropdown { border-radius: 0; }
// 선택 오버, 활성화 
.select2-container--default { 
    .select2-results__option {
        line-height: 40px;
        width: 100% !important;
    }
    .select2-results__option--highlighted[aria-selected] { 
        color: $colorD;background-color: #f8f8f8; 
    }
    .select2-results__option[aria-selected=true] { 
        color:$colorD; background-color: #f8f8f8/*$colorMain*/; 
    }
}
.select2Basic.long { width:100% !important;}

// pop 뎁스 조절 
.select2-dropdown.increasedzindexclass_pop {
  z-index: 999999;
}


// 테이블안 작은 select-box
.select2Basic_s + .select2-container {
    .select2-selection--single { height: 30px; 
        font-size: 14px; font-weight: 400; text-align: center; color: #a0a0a0;
        .select2-selection__rendered {
            span.step0 {color: #a0a0a0;}
            span.step1 {color: #ed0000;}
            span.step2 {color: #00ca09;}
            span.step3 {color: #188efa;}
        }
    }
}
.select2Basic_s.step1 + .select2-container {
    .select2-selection--single .select2-selection__rendered { color: #ed0000;}
}
.select2Basic_s.step2 + .select2-container {
    .select2-selection--single .select2-selection__rendered { color: #00ca09;}
}
.select2Basic_s.step3+ .select2-container {
    .select2-selection--single .select2-selection__rendered { color: #188efa;}
}
.select2Basic_s + .select2-container--default {
    .select2-selection--single .select2-selection__rendered { 
        line-height: 30px - 2px; padding: 0 30px 0 15px;  }
    .select2-selection--single .select2-selection__arrow { 
        height: 30px - 2px; width: 30px; }
    .select2-selection--single .select2-selection__arrow b {
         margin-top: -9px; margin-left: -8px;
    }
}
.select2-dropdown.increasedzindexclass_s {
    .select2-results__options{
        .select2-results__option { font-size: 14px; font-weight: 400; text-align: center; color: #a0a0a0;}
        .select2-results__option:nth-child(1){ color: #ed0000; }
        .select2-results__option:nth-child(2){ color: #00ca09; }
        .select2-results__option:nth-child(3){ color: #188efa; }
    }
}


/* ========================  input file */
.trp.file-box { position: relative; 
    label.screen-reader-text { position:absolute; margin:-1px; padding:0; height:1px; width:1px; overflow:hidden; clip:rect(0,0,0,0); border:0; }
    .trp-Filetext { width: calc( 100% - 120px); height: $formH; padding: 8px 10px 8px 10px;
        font-size: 16px; vertical-align: middle;   border: 1px solid $lineC_d; outline: none; 
        -webkit-appearance: none;
        -moz-appearance: none;
    }
    .btn { position: absolute; top:0px; right:0px; min-width: 110px;  height: $formH; background:#757575; color:#fff; font-size:16px; font-weight: 300;
        -webkit-appearance: none;
        -moz-appearance: none;
    }
    .trp-Filehidden { position: absolute; top:0; left:0; width:100%;  height: $formH;  opacity:0; cursor:pointer; z-index: 1;  }
}





/* ========================  jQuery datepicker */
.datepicker-double { @include clearfix();
    .datepicker-box { float: left; min-width: 160px;}
    .desh { position: relative; float: left; width: 15px; height: 40px;
        &::after { content: ""; display: block; width:9px; height: 1px; 
            position: absolute; top:20px; left: 3px;
            background-color: $lineC_d; 
        }
    }
}
.datepicker-box { position: relative;  display: inline-block;  
    min-width: 150px; width: 160px; height: $formH;
    background-color: #fff; 
    input { width: 100%;  padding-right: 35px; z-index: 1; background-color: transparent; }
    input.br { @include border-radius(0) }
    &:after { content: "" ; display: block; position: absolute; top:8px; right: 8px;
        @include sp_form($icon_calender);
    }
}
body[data-pop=on]{ 
    .ui-datepicker.ui-widget { z-index: 91000 !important;}
}
.ui-datepicker.ui-widget  { border: 1px solid #f3f3f3;  padding: 0; z-index:10000 !important;  @include shadow(0px, 0px, 25px, 0.05); }
.ui-corner-all, .ui-corner-bottom, .ui-corner-br, .ui-corner-right {  border-radius: 0;  }
// 달력 타이틀
.ui-datepicker .ui-datepicker-header {padding: 0; height: 40px; background-color: #ffffff;}
.ui-datepicker-title { height: 40px; line-height: 38px;  color:$colorD;  line-height: 0;
    >span { display: inline-block; margin-top: 18px; line-height: 1.4px;}
    .ui-datepicker-year  { font-weight: 500; color:#3c444f;  }
    .ui-datepicker-month { font-weight: 400; color:inherit;    }
}
.ui-datepicker .ui-datepicker-header { border: 0; border-bottom: 1px solid #f3f3f3; 
    .ui-datepicker-prev  { top:7px; 
        .ui-icon { position: relative; width: 100%; height:100%; margin: 0; padding: 0;
            top:0; left:0;  background: none;
            &::before { content: ""; display: block; width: 10px; height:1px;
                position: absolute; top:9px; left:7px; 
                background-color: #999999; @include rotate(-45deg);
            }
            &:after {content: ""; display: block; width: 10px; height:1px;
                position: absolute; top:16px; left:7px; 
                background-color: #999999; @include rotate(45deg);
            }
        }
    }
    .ui-datepicker-next  { top:7px; 
        .ui-icon { position: relative; width: 100%; height:100%; margin: 0; padding: 0;
            top:0; left:0; background: none;
            &::before { content: ""; display: block; width: 10px; height:1px;
                position: absolute; top:9px; left:8px; 
                background-color: #999999; @include rotate(45deg);
            }
            &:after {content: ""; display: block; width: 10px; height:1px;
                position: absolute; top:16px; left:8px; 
                background-color: #999999; @include rotate(-45deg);
            }
        }
    }
}


// 달력
.ui-datepicker-calendar { 
    tbody {
        tr {
            td {
                .ui-state-default { width: 25px; height: 25px;  line-height: 1.4;
                    text-align: center; border: 0; background-color: #fff; color:#424242; 
                    vertical-align: top;
                    small:hover { text-decoration: underline;  }
                }
            }
            td.ui-state-disabled{  opacity: 1;
                .ui-state-default { width: 25px; height: 25px;  line-height: 1.4;
                    text-align: center; border: 0; background-color: #fff; color:#424242; 
                    vertical-align: top;
                    &:after{ content: ""; display: inline-block; width: 100%; height: 21px;}
                }
            }
            .ui-datepicker-today  .ui-state-default { 
                /* color: #fff; background-color:#f96342; border-radius: 50%;  */
                color:$colorB; font-weight: bold;
            }
            .ui-datepicker-week-end:first-child  .ui-state-default { /* 주일 */  }
            .ui-datepicker-week-end.ui-datepicker-today   .ui-state-default { color:#ffffff; }
        }
    }
}


/* #datepicker_schedule 스케줄 현황  */
#datepicker_schedule{ position: relative;
    /* &:after { content: ""; width: 100%; height: calc( 100% - 100px); 
        position: absolute; bottom:0; left: 0;
        background-color: transparent; 
    } */
    .ui-datepicker.ui-widget  { width: 100%; border: 0px;  padding: 0; z-index:1 !important;  
        @include shadow(0px, 0px, 0px, 0.0); }
    .ui-datepicker .ui-datepicker-header { height: auto; border-bottom: 0;
        .ui-datepicker-prev  { top:7px; width: 40px;height: 40px;
            .ui-icon { position: relative; width: 100%; height:100%; margin: 0; padding: 0;
                top:0; left:0;  @include sp_form($icon_calender_arrow-l);
                &::before { display: none; }
                &:after   { display: none; }
            }
        }
        .ui-datepicker-next  { top:7px; width: 40px;height: 40px;
            .ui-icon { position: relative; width: 100%; height:100%; margin: 0; padding: 0;
                top:0; left:0; @include sp_form($icon_calender_arrow-r);
                &::before { display: none; }
                &:after   { display: none; }
            }
        }
        .ui-state-hover,
        .ui-widget-content .ui-state-hover,
        .ui-widget-header .ui-state-hover,
        .ui-state-focus,
        .ui-widget-content .ui-state-focus,
        .ui-widget-header .ui-state-focus,
        .ui-button:hover,
        .ui-button:focus {
            border: 0px solid #cccccc;
            background: transparent;
            font-weight: normal;
            color: #2b2b2b;            
        }
    }
    .ui-datepicker .ui-datepicker-calendar {
        th { font-size: 15px; font-weight: 400; color:#424242;  }
        td { position: relative; padding: 4px 0;  text-align: center;
           /*  &:after { content: ""; width: 100%; height: 100%; 
                position: absolute; top:0; left: 0;
                background-color: transparent; 
            } */
            a,span { position: relative; display: inline-block; margin-bottom: 10px;   }
            small { display: inline-block; margin-top: -12px; margin-left: -6px; padding: 0px 12px; height: 21px; 
                font-size: 14px; color:#fff;line-height: 21px; font-weight: 300;
                border-radius: 13px; box-sizing: border-box;
            }
            .step1 { background-color: #f99f8c; }
            .step2 { background-color: #f96342; }
            .step3 { background-color: #e13813; } 
        }
    }
    .ui-datepicker-title { height: auto; line-height: 0;  color:$colorD; 
        >span { display: block; margin-top: 0px; line-height: 1.1; text-align: center;}
        .ui-datepicker-year  { font-size: 35px;  }
        .ui-datepicker-month { font-size: 18px; padding: 10px 0 30px; }
    }
    
}

/* 커스텀 카운터 인풋 박스 */
.ui-count {position: relative;display:inline-block; border-radius: 0;
	.ui-countTxt {width:60px; padding-right:30px; padding-left:10px;  text-align: right; background: #fff;
	}
    &:before{content:''; position: absolute; right:1px; top:1px; }    
    >a { position: relative;  width:20px; height:20px; display:block; position: absolute;
        &::after { content: ""; position: absolute; right: 5px; }
    }
    .arrow_up { top:1px; right:0; border-left: 1px solid $lineC_d;
        &::after {  top:7px;  
            border-style: solid; border-width: 0px 4px 5px 4px; height: 0;
            border-color: transparent transparent $colorD transparent;
        }
    }
    .arrow_down { bottom:0px; right:0; border-top: 1px solid $lineC_d; border-left: 1px solid $lineC_d;
        &::after { top:8px;
            border-style: solid; border-width: 5px 4px 0px 4px; height: 0;
            border-color: $colorD transparent transparent transparent;
        }
    }
}

/* ========================  timepicker */
.ui-ampm { position: relative;display:inline-block; width: 50px; min-width:50px;  height: 40px;
    padding: 8px 10px 8px;  border: 1px solid #bfbfbf;
    text-align: center; background: #fff;
}
.timepicker-area { position: relative; display: inline-block; 
    .trp-timepicker{ display: none; position: absolute; top: $formH; 
        width: 180px;
        z-index: 10;
    }
}
.timepicker-box { position: relative;  display: inline-block;  
    min-width: 150px; width: 160px; height: $formH;
    background-color: #fff; 
    input { width: 100%;  padding-right: 35px; z-index: 1; background-color: transparent; }
    input.br { @include border-radius(0) }
    &:after { content: "" ; display: block; position: absolute; top:8px; right: 8px;
        @include sp_form($icon_time);
    }
}



/* ========================  jQuery tooltip */
/* icon_tooltip */
.icon_tooltip { display: inline-block; width: 21px; height: 21px;
    &:after { content: ""; display: block; @include sp_form($icon_tooltip); }   
}

.ui-tooltip{ 
    &.ui-widget-shadow { box-shadow: 0 2px 1px 0 rgba(0,0,0,0.16)}
    &.ui-widget.ui-widget-content {         
        padding: 15px; 
        border: 1px solid #aaaaaa; border-radius: 0;
        .ui-tooltip-content { font-size: 14px; color:$colorB;
            @include  wordBreak-all();
        }
    } 
    font-size: 14px; color:$colorB;
    .arrow { position: absolute;left: 50%; bottom: -15px;
        overflow: hidden;  box-shadow: 0;     
        @include sp_form($dot_tooltip);  
    }
    .arrow.top  {top: -15px; bottom: auto;
        @include sp_form($dot_tooltip-top);  
    }
    //.arrow.left  { left: 20px; right: auto; }
    //.arrow.right { left: auto; right: 20px; }
    .arrow.top:after {bottom: -15px;top: auto;}
}
// 커스텀 툴팁 
.ui-tooltip.tooltip_big{  max-width: 500px;
    .h5 { margin-bottom: 15px; }
    .numbar-list li { font-size: 14; letter-spacing: -1px}
    table { 
        th, td { padding: 5px 10px; font-size: 12px; vertical-align: top; 
            font-size: 14px; text-align: left; line-height: 1.4;
            i { margin-right: 5px;}
            .text_state { font-size: 14px; }
        }
    }
}



/* ========================  jQuery Autocomplete */
body[data-pop=on] .ui-autocomplete.ui-widget {
    z-index: 91000 !important;
    max-height: 300px;
    overflow-y: auto;
    li { padding: 0  10px }
    .ui-autocomplete-category {
        font-size: 13px; color:$colorL
    }
    .ui-menu-item-wrapper { 
        font-size: 13px; color:$colorD;
        strong { font-weight: bold; color:$colorB;}
    }

    .ui-menu-item-wrapper.ui-state-focus,
    .ui-menu-item-wrapper.ui-state-active { 
         border: 1px solid #eee;  background-color: #eee; 
    }

    .searchitme {
        .s_project  {  font-size: 13px; color:$colorL}
        .s_scenario {
            font-size: 13px; color:$colorD;
            strong { font-weight: bold; color:$colorB;}
        }
    }
}
.ui-autocomplete { 
    max-height: 75px;
    overflow-y: auto;
    /* prevent horizontal scrollbar */
    overflow-x: hidden;
    .ui-menu-item-wrapper { font-size: 13px; }
}