// how to use : Use following mixins.
// variable : [X-axis] [Y-axis] [offset_x] [offset_y] [width] [height] [total_width] [total_height] [image_url] [image_name] [folder_name]
    $checkbox-dim: 80px 160px -80px -160px 20px 20px 231px 180px '../images/common/sp_form.png' 'checkbox-dim' 'sp_form';
    $checkbox-off: 170px 84px -170px -84px 20px 20px 231px 180px '../images/common/sp_form.png' 'checkbox-off' 'sp_form';
    $checkbox-on: 120px 160px -120px -160px 20px 20px 231px 180px '../images/common/sp_form.png' 'checkbox-on' 'sp_form';
    $checkbox_s-dim: 213px 76px -213px -76px 18px 18px 231px 180px '../images/common/sp_form.png' 'checkbox_s-dim' 'sp_form';
    $checkbox_s-off: 213px 38px -213px -38px 18px 18px 231px 180px '../images/common/sp_form.png' 'checkbox_s-off' 'sp_form';
    $checkbox_s-on: 213px 0px -213px 0px 18px 18px 231px 180px '../images/common/sp_form.png' 'checkbox_s-on' 'sp_form';
    $dot_tooltip-top: 213px 149px -213px -149px 13px 15px 231px 180px '../images/common/sp_form.png' 'dot_tooltip-top' 'sp_form';
    $dot_tooltip: 213px 114px -213px -114px 13px 15px 231px 180px '../images/common/sp_form.png' 'dot_tooltip' 'sp_form';
    $icon-time: 50px 110px -50px -110px 23px 23px 231px 180px '../images/common/sp_form.png' 'icon-time' 'sp_form';
    $icon_calender: 170px 0px -170px 0px 23px 23px 231px 180px '../images/common/sp_form.png' 'icon_calender' 'sp_form';
    $icon_calender_arrow-l: 60px 0px -60px 0px 40px 40px 231px 180px '../images/common/sp_form.png' 'icon_calender_arrow-l' 'sp_form';
    $icon_calender_arrow-r: 0px 0px 0px 0px 40px 40px 231px 180px '../images/common/sp_form.png' 'icon_calender_arrow-r' 'sp_form';
    $icon_delect: 120px 0px -120px 0px 30px 30px 231px 180px '../images/common/sp_form.png' 'icon_delect' 'sp_form';
    $icon_select_arrow: 93px 110px -93px -110px 23px 23px 231px 180px '../images/common/sp_form.png' 'icon_select_arrow' 'sp_form';
    $icon_tooltip: 170px 43px -170px -43px 21px 21px 231px 180px '../images/common/sp_form.png' 'icon_tooltip' 'sp_form';
    $input_delect: 0px 110px 0px -110px 30px 30px 231px 180px '../images/common/sp_form.png' 'input_delect' 'sp_form';
    $input_error: 50px 60px -50px -60px 30px 30px 231px 180px '../images/common/sp_form.png' 'input_error' 'sp_form';
    $input_search: 0px 60px 0px -60px 30px 30px 231px 180px '../images/common/sp_form.png' 'input_search' 'sp_form';
    $input_success: 120px 50px -120px -50px 30px 30px 231px 180px '../images/common/sp_form.png' 'input_success' 'sp_form';
    $radio-dim: 40px 160px -40px -160px 20px 20px 231px 180px '../images/common/sp_form.png' 'radio-dim' 'sp_form';
    $radio-off: 0px 160px 0px -160px 20px 20px 231px 180px '../images/common/sp_form.png' 'radio-off' 'sp_form';
    $radio-on: 160px 160px -160px -160px 20px 20px 231px 180px '../images/common/sp_form.png' 'radio-on' 'sp_form';

//ancestor
    $sp_form-width: 231px;
    $sp_form-height: 180px;
    $sp_form-image: '../images/common/sp_form.png';
    $sp_form-sprites: ($checkbox-dim, $checkbox-off, $checkbox-on, $checkbox_s-dim, $checkbox_s-off, $checkbox_s-on, $dot_tooltip-top, $dot_tooltip, $icon-time, $icon_calender, $icon_calender_arrow-l, $icon_calender_arrow-r, $icon_delect, $icon_select_arrow, $icon_tooltip, $input_delect, $input_error, $input_search, $input_success, $radio-dim, $radio-off, $radio-on, );
    $sp_form_sprites_name: (checkbox-dim, checkbox-off, checkbox-on, checkbox_s-dim, checkbox_s-off, checkbox_s-on, dot_tooltip-top, dot_tooltip, icon-time, icon_calender, icon_calender_arrow-l, icon_calender_arrow-r, icon_delect, icon_select_arrow, icon_tooltip, input_delect, input_error, input_search, input_success, radio-dim, radio-off, radio-on, );
    $sp_form: (231px, 180px, '../images/common/sp_form.png', $sp_form-sprites, );

//mixin

        %sp_form {
        background-image: url('../images/common/sp_form.png');
        }

        @mixin sp_form($sprite) {
        @extend %sp_form;
        @include spriteSizeSupport1x($sprite);
        @include spriteBackgroundPositionSupport1x($sprite);
        }
