.myproject {

    /* ============ 프로젝트 관리 ============ */
    .col_pro-wrap { @include clearfix();
        .col_left  { float: left; width: 830px; }
        .col_right { float: left; width: 370px; padding-left: 20px; }
    }
    /* ============ 프로젝트 관리 ============ */

    /* ============ 시나리오 관리 ============ */
    .col_sce-wrap {  display: table; table-layout: fixed; border-collapse: collapse; height: 100%;
        .col_row   { display: table-row; }
        .col_left  { display: table-cell; vertical-align: top; position: relative;
            text-align: center; width: 300px;  
            article { height: 100%; }
            .article-box{ height: 100%; }
        }
        .col_right { display: table-cell; vertical-align: top; 
            text-align: center; width: 900px;  padding-left: 20px; }
    }
    /* ============ 시나리오 관리 ============ */
    
    /* == === 검색 == === */
    .search-row { @include clearfix(); .search-col { float: left; } }
    .input-list { @include clearfix();
        li { float: left;}
        .label { font-size: 16px; font-weight: bold; color:$colorD; margin-bottom: 10px;}
        .date-item   {}
        .btnset-item { width: 100%; }
        .range-item { padding: 0 20px; 
            .range-btn_set { @include clearfix();
                .btn { @include border-radius(0px);  }
                .btn_default { float: left; width: 90px; }
                .btn_default + .btn_default { margin-left: 5px; }
                .btn_default.on { background-color: #3c444f; border-color: #3c444f; color:#fff; }
            }
        }
        .search-item { float: right;
            .btn_set-single { width: 122px; }
        }
        .select-item {
            .select-box { width: 100px;}
        }
        .text_search-item { position: relative; 
            width: 100%;            
            .input_icon.search{ width: 100%;}
        }
    }
    /* My Project > 결과 조회 */
    .input-list.result_search{
        .date-item { .datepicker-box { width: 140px; min-width: 140px; }}
        .range-item {}
        .range-btn_set {}
        .text_search-item { margin-left: 20px; width: 285px; }
    }
  
    /* //== === 검색 == === */

    .table-wrap {
        .table_board {
            /* 예약 */
            td.td_reservation {  font-size: 0; line-height: 0;
                i{ margin-top: 4px; 
                    display: inline-block; width: 24px; height: 24px;
                    font-size: 0; line-height: 0;
                } 
            }                                                           
            /* 시나리오 */
            td.td_td_scenario {}                                        
            /* 진행률 */
            td.td_progress { text-align: right; padding-right: 20px; }  
            td { 
                
            }
        }

    }


    /* ============ active 그래프 ================= */
    .total_graph-wrap { 
        .col_1 { width:210px;}
        .col_2 { width:348px;}
        .col_3 { width:210px;}
    }

    /* 진행상황, 완료상황 */
    .g_info-box {
        /* 중앙 info */
        .tit { position: relative;
            span { position: relative; display: inline-block; padding: 0 10px; 
                font-size: 16px; font-weight: bold; background-color: #fff; z-index: 1;}
            &::before { content: ""; display: block; width: 100%; height: 1px;;
                position: absolute; top:10px; left: 0;
                background-color: #cccccc;
            }
        }
        .num  { font-size: 38px; font-weight: bold; color:#7e7e7e; text-align: center; }
        .name { margin-top: -5px;
            font-size: 14px; font-weight: 400; color:$colorD; text-align: center;  }
        .color_1, .color_2, .color_3 {  a:hover { text-decoration: underline; } }
        .color_1 { color:$graphC1; }
        .color_2 { color:$graphC2; }
        .color_3 { color:$graphC3; }
    }
    
    .active_graph-wrap { position: relative; }
    .graph_info-list { 
        /* 원그래프 안 info */
        position: absolute; top:65px; left:60px; width: 90px;
        li {  text-align: center; }
        .info1 { font-size: 16px; color:$colorL; font-weight: 500; letter-spacing: -1px; }
        .info2 { font-size: 36px; color:$colorD; font-weight: 700; line-height: 1.2; }
        .info3 { font-size: 14px; color:$colorD; font-weight: 400; letter-spacing: 0; }
    }

    /* active graph */
    .highcharts-credits { display: none;}
    #active_graph1, #active_graph2 { width: 210px; height: 210px; 
        .highcharts-title  { display: none; }
        .highcharts-tooltip { z-index: 10; 
            .g_tooltip {
                position: relative; width: 130px; height: 130px;
                /* margin-top: 17px; */ margin-left: -23px; text-align: center;
                margin-top: 20px;
                background-color: #fff; border-radius: 100%;
            }
            > span { display: block; width: 100%; }
            .name { display: block; width: 130px; padding-top:22px; padding-left: 0px;
                font-family: $baseFT;
                font-size: 16px; color:$colorL; font-weight: 500; text-align: center;}
            .num { display: block;  width: 130px; padding-top:0px; padding-left: 0px;
                font-family: $baseFT; line-height: 1.2;
                font-size: 36px; color:$colorD; font-weight: 700; text-align: center;}
        }
    }
    /* // ============ active 그래프 ================= */

    /*  ============ bar_graph3-wrap 그래프 ================= */
    .bar_graph3-wrap { width: calc( 100% + 10px); margin-left: -5px;
        .colitem { padding: 15px 5px 0}
        .bar_graph3-area {
            .graph3-top  { padding: 15px 10px; text-align: center; background-color: #3c444f; }
            .graph3-middle  { position: relative;  padding: 16px 0;
                
                .graph_info { li { position: relative; }
                    li + li { vertical-align: mi;
                    &:after { content: ""; display: block; width: 1px; height: 30px;
                        position: absolute; top:10px; left:-1px;
                        background-color: #bfbfbf;
                    }
                }}
            }
            .graph3-body { padding: 0px 10px; overflow: hidden;
                .graph_desc { padding: 30px 0px 25px; border-top: 1px solid #bfbfbf;
                    li.desc_g { position: relative; padding: 0 10px; }
                    li.desc_t { padding: 0 15px; }
                    .bar_round-graph { display: inline-block; }
                    .bar_graph-list, .bar_text-list { position: relative; height: 150px;
                        li { position: relative; width: 100px;}
                        .bar_graph { position: relative; width: 55px;  height: 100%;
                            span { display: block; font-size: 0; line-height: 0; height: 100%; }
                        }
                        //.layout_t { position: absolute; top:0; right: 0; width: 45px; }
                        .bar_val { height: 100%; font-size: 20px; }
                        .item1 .bar_graph span { background-color: $graphC1;}
                        .item2 .bar_graph span { background-color: $graphC2;}
                        .item3 .bar_graph span { background-color: $graphC3;}
                    }
                    .bar_text-list{
                        position: absolute; top:0; right: 0; width: 45px; 
                        li { width: 30px;  min-height: 25px; }
                    }
                    .col3 { li{text-align: center;} }
                    .item1 .bar_round-graph span { background-color: $graphC1;}
                    .item2 .bar_round-graph span { background-color: $graphC2;}
                    .item3 .bar_round-graph span { background-color: $graphC3;}
                }
            }
            .bar_round-graph { position: relative; width: 14px; height: 130px;
                background-color: #efefef; 
                border-top-left-radius:  7px;
                border-top-right-radius: 7px;                
                span { font-size: 0; color:transparent; line-height: 0; 
                    display: block; width: 100%; height: 0;
                    border-top-left-radius:  7px;
                    border-top-right-radius: 7px;
                    position: absolute; bottom: 0; 
                    @include transition(height, 0.3s, ease-out )
                }
            }
           
        

            .h3 { display: inline-block; font-size: 20px; color:#fff; font-weight: 400;}
            .g_name { font-size: 16px; font-weight: 400; text-align: center; }
            .g_data { font-size: 18px; font-weight: 700; text-align: center; }
            .bar_val { font-size: 14px; font-weight: 500; text-align: center;}
            .step    { font-size: 14px; font-weight: 500; text-align: right; color:#a5a5a5; }
            .percent { font-size: 22px; font-weight: 500; text-align: right; color:#4f565e; 
                line-height: 1.2; font-weight: 700;
                em { font-size: 40px; font-weight: 700; }
            }
            .desc-list { position: relative; margin-top: 18px;  @include clearfix();
                dt { float: left; width:50px; 
                    font-size: 16px; font-weight: 400;
                }
                dd { float: left; width:calc( 100% - 50px ); 
                    font-size: 16px; font-weight: 700; text-align: right;
                }
            }
        }
    }
    /* // ============ bar_graph3-wrap 그래프 ================= */
    

    /* ============ 시나리오 그룹 관리 scenario_group-wrap ============ */
    .scenario_group-wrap { margin-top: -50px; margin-bottom: 50px;
        .scenario_group { display: none; background-color: #fff; border-bottom: 1px solid #d2d2d2; 
            @include transition(height, 0.5s, ease-out);
        }
        .body-inner {}
        .setp1 { .inner { padding: 25px 0; text-align: center;} }
        .setp2 { .inner { padding: 30px 0; } }
        .setp3 { .inner { padding: 0px; } }

        // step_1 시나리오관리 추가 
        .scenario_add { 
            text-align: center;
            span { display: inline-block; margin-right: 20px;
                font-size: 22px; font-weight: 500; line-height: 50px; color:#000; 
            }
            .btn { vertical-align: top;}
        }

        // step_2 프로젝트, 그룹 추가 
        .project_group-list { border-bottom: 1px solid #cfcfcf;
            li { position: relative; @include clearfix(); padding: 0; border-top: 1px solid #929292;
                .name-box  { float: left; width: 25%; padding-left: 55px; height: 35px; padding-top: 5px;
                    font-size: 16px; line-height: 30px; }
                .items-box { float: left; width: 75%; height: 35px; height: 40px; padding-top: 5px;}
                .rd25 { min-width: 40px; margin-left: 10px;}
            } 
            .itme-btn_set { padding-right: 70px; height: 35px;
                div.pg_item{ position: relative; display: inline-block; }
                .btn.btn_default { min-width:110px; height: 30px; padding: 0px 25px 0 10px;
                    line-height: 28px; font-size: 14px; color:#a0a0a0; 
                }
                .p_del-btn { position: absolute; top:5px; right:5px; 
                    &:after { content: ""; display: block;  width: 20px; height: 20px;
                        @include sp_icons($icon-item_g_del);
                    }
                }
                div.pg_item.on {
                    .btn.btn_default { background-color: #f95755; border-color: #f95755; color:#fff; }
                    .p_del-btn { &:after { @include sp_icons($icon-item_w_del); } }
                }
            }
            .btn.icon-item_add  { position: absolute; top:5px; right: 0;}
            .btn.icon-item_edit { position: absolute; top:5px; right: 35px;}

            .dep1 { .name-box { font-weight: bold; } }
            .dep2 { .name-box { font-weight: 400;  } background-color: #ececec; }
        }

        // step_3 아이템 로케이션
        .location-list { @include clearfix;
            li { float: left; position: relative; padding:10px; 
                font-size: 16px; color:#949494; font-weight: 300;
            }
            li:first-child { padding-left: 0;}
            li + li { 
                &:after { content: ""; display: block; position: absolute; top:18px; left: -4px;
                    @include sp_icons($icon-loaction);
                }
            }
        }

        .open_close{ width: 50px; margin:-1px auto 0; 
            button { position: relative; width: 50px; height: 30px; 
                background-color: #fff; border: 1px solid #d2d2d2; border-top: 0;
                &:after { content: ""; display: block; position: absolute; top:5px; left: 15px;;
                    @include sp_icons($icon-scenario_open_close);
                }
            }
        }
        
        
        .scenario_open + .open_close{ 
            button { 
                &:after { @include rotate(180deg); }
            }
        }
    }
    /* //============ 시나리오 그룹 관리 scenario_group-wrap ============ */

    
    /* ============ 시나리오 관리 menu ============ */
    .menu_col { 
        .menu_left  { width: 26.3333%; height: 100%; vertical-align: top; }
        .menu_right { width: 73.6667%; height: 100%; padding: 18px; padding-right: 10px; border-left: 1px solid #dbdbdb; vertical-align: top; }
    }
    .icon-list { position: relative;
        li { border-bottom: 1px solid #dbdbdb;  text-align: center;
            >button { display: inline-block; width: 100%; padding: 12px 0 10px; }
            .icon { }
            span  { display: block; margin-top: -5px; 
                font-size: 14px; font-weight: 400;  
            }
        }
    }
    .search-box .input_icon.search { input { @include border-radius(0); }  }
    .search-box { padding-right : 8px; }
    .schedule_check-box { padding : 7px 0 5px; padding-right: 8px; text-align: left;
        label { font-size: 14px;}
        label:hover { text-decoration: underline; }
    }
    .secnario_list-wrap { margin-top: 0px;
       
        .inner { position: relative; padding: 8px 0 6px;   text-align: left; 
            .checkbox_s-box  { vertical-align: top; }
            .icon-star_s     { vertical-align: top; }
            .label           { vertical-align: top; margin-top: 2px; display: inline-block; } 
        }
        .secnario_top { position: relative; padding: 0px; margin-right: 8px;
            border-top:1px solid #707070; border-bottom:1px solid #707070;
            text-align: left;
            a { height: 18px; }
            .label { font-size: 12px; color:$colorB }
            .label:hover { text-decoration: underline; }
            .rd21 { position: absolute; top:8px; right:0; }
        }
        .secnario_body { border-bottom:1px solid #707070; 
            .scnario_dep1 { position: relative;  height: 942px; }
            .scnario_dep1-inner{ padding-right: 8px;
                >.secnario-list { 
                    > li { > .inner { 
                        a { height: 18px; }
                        a:hover, .label:hover { text-decoration: underline; 
                            span { text-decoration: underline; }
                        }
                        .label { display: inline-block; max-width: 125px; 
                            font-size: 12px; color:$colorB; 
                            @include ellipsis();}}
                    }
                    > li.data-none { > .inner { 
                        span { max-width: 100%;}
                        .btnRound { float: right; }
                    }} 
                    > li:first-child { > .inner { position: relative;
                        
                        &::after { content: "" ;display:block; width: 162px; height: 1px;
                            position: absolute; bottom: 0; right:0;
                            background-color: #707070; 
                            }
                        } 
                    } 
                }
                .secnario-list { 
                    >li:first-child { .inner { border-top: 0; }}
                    li { .inner { border-top:1px solid #d9d9d9;
                        .checkbox_s-box  { vertical-align: top; }
                        .icon-star_s     { vertical-align: top; }
                        .label           { vertical-align: top; } 
                    }} 
                    
                    .scnario_dep2 { max-height:250px; }
                    .secnario-list { 
                        >li:first { .inner{ border-top: 0; }}
                        >li { padding-left: 20px; 
                            .inner { border-top:1px solid #d9d9d9;}
                            .ellip-box { max-height: 30px;   overflow: hidden;
                                a:hover, .label:hover { text-decoration: underline; .label{ text-decoration: underline; }}
                                .label { display: block; max-height: 30px;
                                    position: relative; width: 110px; 
                                    overflow: hidden;
                                    font-size: 12px; font-weight: 400; color:#949494; 
                                    vertical-align: middle;
                                }
                            }
                        } 
                    }
                }
            }// scnario_dep1
        }
        .layout_r {
            .layout_c { position: relative; text-align: left;  line-height: 1.2;  width: 100%;}
            .layout_c:nth-child(1){ width: 45px;  }
        }
        .label { vertical-align: middle; font-size: 12px; font-weight: 500; line-height: 1.2; }
        .checkbox-box { vertical-align: middle; }
        .icon-star { margin: -1px 2px 0; vertical-align: middle; }
    }
    /* // ============ 시나리오 관리 menu ============ */

    

    /* ============ 시나리오 관리 content ============ */
    // 시나리오 C
    .scenario_info-wrap { padding:25px 20px; 
        .icon-msg { position: relative; padding-left: 2px;;
            &::after { position: absolute; top:3px; }
        }
    }

    // 시나리오 스텝
    .secnario_step-wrap { padding:40px 24px; 
        .layout_col { @include clearfix();
            .layout_left  { width: 785px; height: 100%;
                @include transition(width, 0.3s, ease-out); }
            .layout_right { width: 45px;  height: 100%; padding-left: 10px; overflow: hidden; 
                @include transition(width, 0.3s, ease-out); 
            }
        }
        .step_thumbnail-area{ position: relative; padding: 0 35px;
            .step-list{
                li {
                    .li-inner{ position: relative; display: inline-block;
                        font-size: 0; line-height: 0;
                        a { display: block; position: relative;
                            img { width: 139px; height: 77px;}
                            .setp_num {  display: inline-block; padding: 2px 10px;
                                position: absolute; top:5px; left: 5px;
                                line-height: 1.2; font-size: 12px; text-align: center; 
                                box-sizing: border-box;  color:#fff; border-radius: 10px;
                                background-color: rgba(70,70,70,0.7); z-index: 2;
                            }
                            .layout_t.setp_msg { width: 100%; height: 100%;
                                position: absolute; top:0px; left: 0px;
                                background-color: rgba(20,70,255,0.7); z-index: 1;
                                .layout_r {  }
                                .layout_c { width: 100%; height: 77px;
                                    vertical-align: middle; color :#fff; font-size: 12px; }
                            }
                            &:after { content: ""; display: block; width: 100%; height: 100%;
                                position: absolute; top:0; left: 0;
                                border: 1px solid #c5c5c5; box-sizing: border-box; z-index: 3;
                            }
                        }
                    }
                }
                li.current {
                    .li-inner{ a:after { border: 3px solid #375ac0; } }
                }
            }
            .bx-step_controls { position: relative;
                >div {  position: absolute; top:-77px; width: 30px;height: 77px; }
                .bx-step_prev { left:-35px;}
                .bx-step_next { right:-35px;}
                a { display: block; width: 30px;height: 77px;
                    font-size:0; line-height: 0; background-color: #c5c5c5;
                    &:after { content: ""; display: block; 
                        position: absolute; top: 50%; left:50%; 
                        margin-top: -10px; margin-left: -6px;
                    }
                } 
                a:hover { background-color: #757575; }
                .bx-prev { &:after { @include sp_icons($gallery_arrow_left);  } } 
                .bx-next { &:after { @include sp_icons($gallery_arrow_right);  } } 
            }
        }
        .step_view-area { margin-top: 5px;
            .inner{  border: 1px solid #c5c5c5; height: 444px; }
            .img { position: relative; 
                img { width: 100%;  box-sizing: border-box; }
            }
            .img.img_check { position: relative;
                background: url(/resources/images/myproject/data_check_bg.png) 0 0 repeat;
                img {   }
            }
        }
        .secnario_case-area { position: relative; 
            display: table; table-layout: fixed; border-collapse: collapse; width: 100%; height: 100%;
            .case_row   { display: table-row;  }
            .case_left  { position: relative; vertical-align: top;
                display: table-cell; height: 100%; min-height: 526px; width: 35px; }
            .case_right { position: relative; vertical-align: top;
                display: table-cell; height: 100%; min-height: 526px; width: 288px; 
                .inner { position: relative; padding: 15px; border: 1px solid #c1c1c1; border-left: 0;
                    height: 526px; min-height: 100%; }
            }

            .case_btn {position: relative; display: block; 
                width: 100%; height:100%; min-height: 526px; padding-top:200px;
                box-sizing: border-box; 
                background:url(/resources/images/myproject/case_btn-bg.jpg) 0 0 no-repeat;
                background-size: 100% 100%;
                @include transition(padding-top, 0.3s, ease-out); 
                >span {  font-size: 16px; color:#ffffff; font-weight: 400; text-align: center;
                    vertical-align: middle;}
                &::before { content: ""; display: inline-block;
                    @include sp_icons($gallery_arrow_right);
                }
            }

            .case_top  { text-align-last: left;
                input { padding: 0px 10px 0px; @include border-radius(0);}
                .name  { input { height: 35px;  font-size: 20px; font-weight: 500; }
                    font-size: 20px; font-weight: 500; color:#464646; text-align: left;}
                .desc  { input { height: 30px; font-size: 14px; }
                    margin-top: 5px;
                    font-size: 14px; font-weight: 400; color:#949494; text-align: left;}
            }
            .case_middle { margin-top: 10px;  padding-bottom: 50px; 
                max-height: 309px; 
                .option_item-list {
                    li { border-bottom:1px solid #707070; }
                    li:first-child { border-top:1px solid #707070; }
                }
                .table-wrap { display: none; padding-left: 10px; 
                    border-top:1px solid #d6d6d6;                     
                    .form_table.type_s { border-top: 0;
                        tr { 
                            td { font-size: 14px; color:#666666; }
                            td + td { width: 110px; /* padding-right: 0;  */} 
                        }
                    }
                }
                .item_name {
                    a { position: relative; display: block; padding: 12px 10px; padding-right: 30px;
                        font-size: 16px; font-weight: 500; color:#3d3d3d;
                        text-align-last: left; 
                        &:after { content: ""; display: block; 
                            position: absolute; top:18px; right:8px;
                            @include sp_icons($accordion_arrow_down);
                        }
                    }
                }
                .acd_on .item_name {
                    a:after { @include sp_icons($accordion_arrow_up); }
                }
            }
            .case_bottom {  position: absolute; bottom: 20px;  width: 257px;
                .btn_set{ width: 100%; }
            }
        }
    }
    .secnario_step-wrap.case_open { 
        .layout_col { 
            .layout_left  { width: 497px; }  // 785px +- 285
            .layout_right { width: 333px; }  //  45px +- 285  
        }
        .case_btn {
            &::before { @include sp_icons($gallery_arrow_left); }
        }
    }

    
    // 시나리오 리스트 
    .scenario_list-wrap {  padding: 25px 20px;
        .tableInfoTop { 
            .btn_dark, .btn_blue { min-width: 144px; }
        }
        .table_body { height: 123px;}
    }
    /* // ============ 시나리오 관리 content ============ */



    /* ============ 스케줄 관리 content ============ */
  /*   .search-row.schedule{
        .col1 { width: 700px;}
