    .search-row.schedule{
        .col1 { width: 515px;
            .input-list .range-item {
                padding: 0 0 0 20px;
            }
        }
        .col2 { width: 685px;  padding-left: 30px;}
    }
    .table_btn-info { position: relative; }
    .calendar-box { display: inline-block;
        .article-box { display: none;  width: 350px; 
            position: absolute; top:85px; right:20px;
            background-color: #fff;
            z-index: 10;
        }
    }
    .calendar-box.calendar_open{ 
        .article-box { display: block;}
        .calendar-wrap { padding: 20px; min-height: 471px;  @include shadow(0,0,5px, 0.1) }
    }
    /* // ============ 스케줄 관리 content ============ */
    

    
    /* ============ 결과조회 content ============ */
    /* 검색 */
    .search-row.result{
        .col1 { width: 755px;}
        .col2 { width: 445px;  padding-left: 30px;}
    }

 



    /* 수행결과 리스트 */
    .result-wrap {
        .layout_col { @include clearfix();
            .layout_left  { position: relative; width: 1155px; height: 100%; vertical-align: top;
                @include transition(width, 0.3s, ease-out); }
            .layout_right { position: relative; width: 45px;  height: 100%; vertical-align: top;
                padding-left: 10px; overflow: hidden; 
                @include transition(width, 0.3s, ease-out); 
            }
        }
        .table-info { position: relative; padding:30px 10px 0px;
            .ab-r { top:20px; right:25px; }
            .info_dep2-list { overflow: hidden;}
        }
        .result_case-area { position: relative; 
            display: table; table-layout: fixed; border-collapse: collapse; width: 100%; height: 100%;
            .case_row   { display: table-row;  }
            .case_left  { position: relative; vertical-align: top;
                display: table-cell; height: 100%; min-height: 580px; width: 35px; }
            .case_right { position: relative; vertical-align: top;
                display: table-cell; height: 100%; min-height: 580px; width: 288px; 
                .inner { padding: 15px 15px 20px; border: 1px solid #c1c1c1; border-left: 0;
                    height: 580px; min-height: 100%;  background-color: #fff;}
            }

            .case_btn {position: relative; display: block; 
                width: 100%; height:100%; min-height: 580px; padding-top:200px;
                box-sizing: border-box; 
                background:url(/resources/images/myproject/case_btn-bg.jpg) 0 0 no-repeat;
                background-size: 100% 100%;
                @include transition(padding-top, 0.3s, ease-out); 
                >span {  font-size: 16px; color:#ffffff; font-weight: 400; text-align: center;
                    vertical-align: middle;}
                &::before { content: ""; display: inline-block;
                    @include sp_icons($gallery_arrow_right);
                }
            }

            .case_top  { text-align-last: left;
                input { padding: 0px 10px 0px; @include border-radius(0);}
                .name  { input { height: 35px;  font-size: 20px; font-weight: 500; }
                    font-size: 20px; font-weight: 500; color:#1C4BB7; text-align: left;}
                .desc  { input { height: 30px; font-size: 14px; }
                    margin-top: 5px;
                    font-size: 14px; font-weight: 400; color:#949494; text-align: left;}
            }
            .case_middle { margin-top: 10px;  padding-bottom: 50px; 
                min-height: 309px; 
                .option_item-list {
                    li { border-bottom:1px solid #707070; }
                    li:first-child { border-top:1px solid #707070; }
                }
                .table-wrap { display: none; padding-left: 10px; 
                    border-top:1px solid #d6d6d6;                     
                    .form_table.type_s { border-top: 0;
                        tr { 
                            td { font-size: 14px; color:#666666; }
                            td + td { width: 110px; /* padding-right: 0;  */} 
                        }
                    }
                }
                .item_name {
                    a { position: relative; display: block; padding: 12px 10px; padding-right: 30px;
                        font-size: 16px; font-weight: 500; color:#3d3d3d;
                        text-align-last: left; 
                        &:after { content: ""; display: block; 
                            position: absolute; top:18px; right:8px;
                            @include sp_icons($accordion_arrow_down);
                        }
                    }
                }
                .acd_on .item_name {
                    a:after { @include sp_icons($accordion_arrow_up); }
                }
            }
            .case_bottom {  /* position: absolute; bottom: 20px;  */
                width: 257px;
                .btn_set{ width: 100%;  .btn { min-width: 90px;}}
            }
        }

        /* 시나리오 레이어 */
        .view_layer { display: none; width: 100%; height: 100%; margin-top: 0;
            position: absolute; top:0; left: 0;
            background-color: #fff;
            >.inner { padding: 20px 25px; 
                .col_left  { padding-right:8px; }
                .col_right { padding-left:8px; 
                    .step_view-area {  padding-bottom: 10px;
                        >.inner { height: 327px; }
                    }
                }
            }
            .tit-box { position: relative; padding:10px 0;
                .h3 { font-size: 20px; font-weight: 500; text-align: left;}
                .ab-r { top:0px;  }
            }
            .step_thumbnail-area{ position: relative; padding: 5px 20px 10px;
                .step-list{
                    li {
                        .li-inner{ position: relative; display: inline-block;
                            font-size: 0; line-height: 0;
                            a { display: block;
                                img { width: 73px; height: 40px;}
                                &:after { content: ""; display: block; width: 100%; height: 100%;
                                    position: absolute; top:0; left: 0;
                                    border: 1px solid #c5c5c5; box-sizing: border-box;
                                }
                            }
                        }
                    }
                    li.current {
                        .li-inner{ a:after { border: 3px solid #375ac0; } }
                    }
                }
                .bx-step_controls { position: relative; 
                    >div {  position: absolute; top:-40px; width: 15px; height: 40px; }
                    .bx-step_prev { left:-20px;}
                    .bx-step_next { right:-20px;}
                    a { display: block; width: 15px;height: 40px;
                        font-size:0; line-height: 0; background-color: #c5c5c5;
                        &:after { content: ""; display: block; 
                            position: absolute; top: 50%; left:50%; 
                            margin-top: -5px; margin-left: -3px;
                        }
                    }
                    a:hover { background-color: #757575; }
                    .bx-prev { &:after { @include sp_icons($gallery_arrow_left_s);   } } 
                    .bx-next { &:after { @include sp_icons($gallery_arrow_right_s);  } } 
                }
            }
            .step_view-area { margin-top: 5px;
                .inner{  border: 1px solid #c5c5c5; height: 282px; }
                .img { position: relative; 
                    img { width: 100%;  box-sizing: border-box; }
                }
                .img.img_check { position: relative;
                    background: url(/resources/images/myproject/data_check_bg.png) 0 0 repeat;
                    img {   }
                }
            }
            .table-wrap {
                .btn_set { margin-top: 15px;
                    .btn { width: 125px; }
                }
            }
        }
    }
    .result-wrap.case_open { 
        .layout_col { 
            .layout_left  { width: 870px; }  // 1155px +- 285
            .layout_right { width: 333px; }  //  45px  z+- 285  
        }
        .info_dep2-list { display: none; }
        .case_btn {
            &::before { @include sp_icons($gallery_arrow_left); }
        }
    }
    .result-wrap.case_open.layer_open {  
        .layout_col { 
            .layout_left  { width: 905px; }  // 1155px +- 285  +- 35
            .layout_right { width: 298px; }  //  45px  z+- 285 +- 35 
        }

        .result_case-area {
            .case_left { display: none; }
            .case_right { border-left: 1px solid #c1c1c1;; }
        }
    }
    /* //============ 졀과조회 content ============ */
    
    
    /* ============ 설정 content ============ */
    /* 검색 */
    .search-row.setup{
        .col1 { width: 420px;}  
        .col2 { width: 780px;  padding-left: 30px;} 
    }
    .setup_list-wrap{
        .list_btn  { }
        .edit_btn  { display: none; }
        .txt_data  { line-height: 30px; }
        .edit_data { display: none; }

        .txt_data, .edit_data {
            font-size: 14px; font-weight: 400; text-align: center; color: #a0a0a0; 
            input { font-size: 14px; font-weight: 400; text-align: center; color: #a0a0a0;}
        }
    }
    .setup_list-wrap.setup_edit{
        .list_btn  { display: none; }
        .edit_btn  { display: block; }
        .txt_data  { display: none; }
        .edit_data { display: block; }
    }
    /* ============ //설정 content ============ */

}